<template >
  <mt-popup
  v-model="item.isPicker"
  position="bottom"
  :modal="true"
  :closeOnClickModal="true"
  class="popup-bottom">
  <h3 class="title">{{item.name}}</h3>
  <mt-picker   :slots="item.slots" @change="onValuesChange"></mt-picker>
  <el-row class="btn-group">
    <el-col :span="12" style="text-align:center">
      <mt-button type="default" class="left" size="normal" @click="cancelValue">取消</mt-button>
    </el-col>
    <el-col :span="12" style="text-align:center">
      <mt-button type="default" class="right" size="normal" @click="confirmValue">确定</mt-button>
    </el-col>
  </el-row>
</mt-popup>
</template>

<script>
export default {
  props:{
    item:Object,
  },
  data (){
    return {
      active_value:this.item.normal_min|| this.item.sub_min,
    }
  },

  methods:{
    onValuesChange(picker, values) {

      if(values.length==2){
        if(values[0]==undefined){
          values[0]=this.item.slots[0].values[0];
        }
        if(values[1]==undefined){
          values[1]=this.item.slots[2].values[0];
        }
        this.active_value =values[0]+"."+values[1];
      }else{
          this.active_value =values[0];
      }

    },
    cancelValue () {
      // this.active_value = null;
      this.item.isPicker = false;
    },
    confirmValue (){
      this.item.value = Number(this.active_value);
      // this.active_value = null;
      this.item.isPicker = false;
    }
  }
}
</script>

<style  lang="scss">
.popup-bottom{
  width: 100%;
  background: #f0f0f0;
  height: 300px;
  .title{
    margin: 0px;
    text-align: center;
    height: 20px;
    line-height: 20px;
    color:#b01421;
    font-size: 16;
    padding: 5px ;
    border-bottom: 1px solid #cccccc;
  }
  .picker {
    .picker-item{
      color:#b4b4b4;
      font-weight: 100 !important;
    }
  .picker-item.picker-selected{
    height: 50px;
    color:#606060;

    line-height: 50px;
    font-size: 30px;
  }
    .picker-center-highlight{
        height: 50px;
    }
    .picker-center-highlight:after,.picker-center-highlight:before{
          background-color: #b3b3b3;
    }
  }
  .btn-group{
    margin-top: 20px;
    button.right{
      padding:0px 20px;
      background: #b01421;
      color:#fff;
    }
    button.left{
      padding:0px 20px;

      border: 1px solid #b3b3b3;
      color: #b01421;
      background: transparent;
    }

  }
}
</style>



