//路由文件
//引入路由组件
import login from "./components/member/Login.vue"
import index from "./components/Index.vue"
import register from './components/register/register.vue'
import informate from './components/register/informate.vue'
import hobby from './components/register/hobby.vue'
import job from './components/register/job.vue'
import rules from './components/register/rules.vue'
import healthData from './components/health/healthData.vue'
// import addHealthData from './components/health/addHealthData.vue'
// import newHealthData from './components/health/newHealthData.vue'
import remindHealth from './components/health/remindHealth.vue'
import mge_caese from './components/caese/mge_caese.vue'
import uploder from './components/caese/uploder.vue'
import cards from './components/index/cards.vue'
import dataSummary from './components/index/dataSummary.vue'
import focusFamily from './components/focus/focusFamily.vue'
import addCall from './components/focus/addCall.vue'
import familyData from './components/focus/familyData.vue'
import editFocus from './components/focus/editFocus.vue'
import medicine from './components/medicine/medicine.vue'
import addMedicine from './components/medicine/addMedicine.vue'
import seeinfo from './components/information/seeinfo.vue'
import editInfo from './components/information/editInfo.vue'
import changePassword from './components/information/changePassword.vue'
import setPassword from './components/information/setPassword.vue'

//定义路由
const routes =[
 {
  path:"/login",
  component:login,
  meta:{
    title:"登录"
  }
},
 {
   path:"/index",
   component:index,

   children:[
     {
       path:"",
       component:cards,
       meta:{
         title:"我的数据"
       }
     },
     {
       path:"/dataSummary",
       component:dataSummary,
       meta:{
         title:"数据汇总"
       }
     },
   ]
 },
 {
   path:"/healthData",
   component:healthData,
   meta:{
     title:"健康数据"
   }
 },

 // {path:"/addHealthData",component:addHealthData},
 // {path:"/newHealthData",component:newHealthData},
 {
   path:"/register",
   component:register,
   meta:{
     title:"注册"
   }
 },
 {
   path:"/informate",
   component:informate,
   meta:{
     title:"注册"
   }
 },
 {
   path:"/hobby",
   component:hobby,
   meta:{
     title:"注册"
   }
 },
 {
   path:"/job",
   component:job,
   meta:{
     title:"注册"
   }
 },
 {
   path:"/rules",
   component:rules,
   meta:{
     title:"条款及规则"
   }
 },
 {
   name:"itemInput",
   path:"/itemInput/:id/:title",
   component:require('./components/health_item/itemInput.vue'),
 },
 {
   name:"itemSelect",
   path:"/itemSelect/:id/:title",
   component:require('./components/health_item/itemSelect.vue'),
 },
 {
   path:"/member",
   component:require('./components/member/member.vue'),
   meta:{
     title:"会员中心"
   }
 },
 {
   path:"/startRegister",
   component:require('./components/member/startRegister.vue'),
   meta:{
     title:"会员中心"
   }
 },
 {
   path:"/focusFamily",
   component:focusFamily,
   meta:{
     title:"关注"
   }
 },
 {
   path:"/addCall",
   component:addCall,
   meta:{
     title:"关注"
   }
 },
 {
   path:"/familyData/:id/:title",
   name:"familyData",
   component:familyData,
 },
 {
   path:"/editFocus/:focu_id/:appellation",
   name:"editFocus",
   component:editFocus,
   meta:{
      title:"编辑关注"
   }
 },
 {
 	path:"/remindHealth/:id/:title",name:"pathrRemind",component:remindHealth,
	meta:{
	 title:"血压提醒"
	}
},
 {
 	path:"/mge_caese",component:mge_caese,
	meta:{
		title:"管理病历"
	}
},
 {
 	path:"/uploder",component:uploder,
 	meta:{
		title:"照片预览"
	}
 },
 {
 	path:"/medicine",component:medicine,
 	meta:{
		title:"服药管理"
	}
},
 {
 	path:"/addMedicine/:id/:name/:usage",component:addMedicine,name:"addMedicine",
 	meta:{
		title:"服药提醒"
	}
},
{
  path:"/seeinfo",component:seeinfo,
  meta:{
    title:"个人信息"
  }
},
{
  path:"/editInfo",component:editInfo,
  meta:{
    title:"个人信息"
  }
},
{
  path:"/changePassword",component:changePassword,
  meta:{
    title:"个人信息"
  }
},
{
  path:"/setPassword",component:setPassword,
  meta:{
    title:"个人信息"
  }
},
 {path:"*",redirect:'/index'}
]

export default routes



