<template>
	<div class="member" v-cloak>
		<div class="mem-cainter">
	  		<div style="background: #DA2905;padding:30px 0px;">
	  			<div class="user-photo">
					<img :src="memberList.wx_avatar" id="img" slot="center" v-if="isPhoto">
					<img src="../../assets/photo.png" slot="center" v-else>
				</div>
				<div class="user-name">
					<p>{{memberList.truename}}</p>
				</div>
				<div class="user_card">
					<qrcode v-if="memberList.user_vip_info.card_number" :size="74"  :val="memberList.user_vip_info.card_number" style="width:74px;height:74px;float:left;"></qrcode>
		  			<p>会员卡号&nbsp;
		  				<span>{{memberList.user_vip_info.card_number}}</span>
		  			</p>
				</div>
	  		</div>
			<div class="mem-background"></div>
		</div>
		<el-row class="el2">
	  		<el-col :span="12" class="el3">
	  			<div class="el3-text" v-on:click="seeClick">
              		<span class="seeinfo">
              			<i class="iconfont icon-zhanghu1"></i>&nbsp;个人信息
              		</span>
	  			</div>
	  		</el-col>
	  		<el-col :span="12" class="el3">
	  			<div class="el3-text">
		  		    <span>{{memberList.user_vip_info.vip_score}}</span>&nbsp;积分
	  			</div>
	  		</el-col>
		</el-row>
  	</div>
</template>

<script>
import Qrcode from './Qrcode.vue'
import { Indicator } from 'mint-ui';

export default {
	components: {
    Qrcode
  },
	data () {
		return {
			isPhoto:true,
			memberList:{
				user_vip_info:{}
			}
		}
	},
	methods: {
		getMember (){
			Indicator.open('正在加载');
			//获取用户信息
			var resource = this.$resource(this.$store.state.serverIP+'/users/get_user_info/?'+location.search.substring(1).split("&")[0]);
			resource.get().then((response) => {
				response.data.user_vip_info.card_number+="";
				this.memberList = response.data;
				Indicator.close();
			});
		},

		seeClick (){
			window.localStorage.memberList = JSON.stringify(this.memberList);
			this.$router.push('/seeInfo');
		}
	},
	mounted (){
		this.getMember();
		var resource = this.$resource(this.$store.state.serverIP+'/users/vip_mark?'+location.search.substring(1).split("&")[0]);
        resource.get().then((callback) => {
	        if (callback.data != 1) {
	        	Indicator.close();
	            this.$router.replace('/startRegister');
	        }
      	});
	}
}
</script>

<style scoped lang="scss">
.member .mem-cainter{
	width: 100%;
	overflow: hidden;
	color: #fff;
	font-family: "微软雅黑";
	font-size: 16px;
	margin: 0;
  .user-name{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .user-photo{
    margin: 0px auto;
    height: 65px;
    width: 65px;
    border-radius: 50%;
   	background-color:#f0f0f0;
    overflow: hidden;
    img{
      width: 65px;
      height: 65px;
    }
  }
  .user_card{
  	margin:auto;
  	overflow:hidden;
  	padding-left:30px;
  	p {
  		margin:0px;
  		float:left;
  		padding-left:10px;
  		height:74px;
  		line-height:74px;
  	}
  }
}
.botm-cainter{
	width: 100%;
	height: 50%;
}
.el2{
	margin: 20px auto;
}
.el2 .el-col{
	height:160px;
}
.el2 .el-col div{
	margin: 0 auto;
	text-align: center;
}
.el3:first-child{
	border-right: 1px solid #ddd;
}
.el3 a{
	text-decoration: none;
	color: #000;
}

.el3 {
  .el3-text{
    padding: 40px 0px;
    height:40px;
    line-height:40px;
  	font-size: 20px;
    span{
      font-size: 28px;
      vertical-align: middle;
    }
    .seeinfo{
    	display:inline-block;
    	font-family:'微软雅黑';
    	font-size:18px;
    	height: 26px;
      	line-height: 26px;
      	padding: 0px 10px;
      	color: #323232;
      	i{
			color:#7acdee;
			font-size:25px;
			vertical-align: middle;
      	}
    }
  }
}

.mem-background{
	height: 55px;
	background:url("./../../assets/backred_text.png") no-repeat;
	background-size: 100%;
	margin: 0;
	margin-top: -1px;
}
</style>



