<template>
	<div class="create">
		<mt-header title="修改密码" class="createName">
			<router-link to="/changePassword" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="msg">
			<mt-field label="新密码" placeholder="请输入密码" type="password" v-model="form.password"></mt-field>
			<mt-field label="确认密码" placeholder="请输入确认密码" type="password" v-model="confirm_password"></mt-field>
		</div>
		<div class="next">
			<router-link to="/">
			    <mt-button class="next-btn" @click.native="submitClick">完成</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				form: {
					password: '',
					code:this.$route.query.code || ""
				},
				confirm_password: ''
			}
		},
		computed:{
			checkPassword:function() {
				return (this.form.password!=''&&this.form.password == this.confirm_password)
			}
		},
		methods: {
			submitClick (){
				window.localStorage.form = JSON.stringify(this.form);
			}
		}
	}
</script>
<style>

</style>



