<template>
  <div class="summary">
    <div class="summary-title">
      <span class="current">
        {{current_item.name || "..."}}
      </span>
      <div class="select" @click="popupVisible=true">
        查看更多

      </div>
    </div>

    <div class="warp">
      <p class="no-message" v-if="item_records.length<=0">暂无记录</p>
      <div class="records" v-else>
          <h2 style="text-align:center;">{{current_item.name + "统计"}}</h2>
          <div class="records-item" v-for="record in item_records">
            <p class="records-date">{{record.record_date}}</p>
            <el-row class="records-data" v-for="items in record.record_content">
              <el-col :span="24/(current_item.subitems.length+1)" class="data" v-for="item in items.subitem_content" v-if="current_item.subitems.length>0">
                <div class="left">
                  <p>{{item.name}}</p>

                </div>
                <div class="right">
                  <mt-badge size="normal" type="error" v-if="item.ex_min">偏低</mt-badge>
                  <mt-badge size="normal" type="error" v-if="item.ex_max">偏高</mt-badge>
                </div>
                <p>{{item.content}}<span style="font-size:8px;">({{current_item.unit}})</span></p>
              </el-col>
              <el-col :span="16" class="data"  v-if="current_item.subitems.length<=0">

                <div class="left">
                  <p>{{items.name}}</p>

                </div>
                <div class="right">
                  <mt-badge size="normal" type="error" v-if="items.ex_min">偏低</mt-badge>
                  <mt-badge size="normal" type="error" v-if="items.ex_max">偏高</mt-badge>
                </div>
                <p>{{items.content}}<span style="font-size:8px;">({{current_item.unit}})</span></p>
              </el-col>
              <!-- <el-col :span="8" class="data" v-else>
                <p>{{item.name}}</p>
                <p>{{item.content}}</p>
              </el-col> -->
              <el-col class="date" :span="24/(current_item.subitems.length+1)" v-if="current_item.subitems.length>0">
                <p>时间</p>
                <p>{{items.subitem_time||items.created_at||""}}</p>

              </el-col>
              <el-col class="date" :span="8" v-else>
                <p>时间</p>
                <p>{{items.subitem_time||items.created_at||""}}</p>

              </el-col>
            </el-row>
          </div>
      </div>
    </div>
    <mt-popup  style="width:100%;height:calc(100% - 110px);"
      v-model="popupVisible"
      position="bottom"
      >
      <div
      class="select-item"
      :class="{'selected':item.id == current_item.id}"
       v-for="item in healthList"
      @click="current_item=item;popupVisible=false;">
        {{item.name}}
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
export default {
  data (){
    return {
      popupVisible:false,
      healthList:[],
      item_records:[],
      current_item:{}
    }
  },
  methods:{
    getHealthitem (){
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?status=1&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        this.healthList = callback.data
          Indicator.close();

        if(this.healthList.length>0){
          this.current_item = this.healthList[0]

        }
      },error=>{
        Indicator.close();

      });
    },
  },
  watch:{
    current_item:function (item){
      Indicator.open('加载中...');
      var resource = this.$resource(this.$store.state.serverIP +"/health_item_records/item_records?health_item_id="+item.id+"&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        Indicator.close();
        this.item_records = callback.data.item_records;
      },error=>{
        Indicator.close();
      });
    }
  },
  mounted () {
    Indicator.open('加载中...');
    this.getHealthitem()
  }
}

</script>

<style scoped lang="scss">
.summary{
  padding: 0px 16px;
  .summary-title{
    padding:10px 0px;
    border-bottom: 1px solid #ccc;
    overflow: hidden;
    .current{
      height: 26px;
      line-height: 26px;
      padding: 0px 10px;
      color: red;
      float: left;
      border-radius: 5px;
      border:1px solid red
    }
    .select{
      height: 26px;
      line-height: 26px;
      padding: 0px 10px;
      color: red;
      float: left;
      margin-left: 20px;
      border-radius:13px;
      border:1px solid #323232
    }
  }
  .mint-popup{
    background: rgba(0,0,0,.5);
    .v-modle{
      background: transparent;
    }
  }
  .select-item{
    margin-left: 15px;
    margin-top: 15px;
    height: 26px;
    line-height: 26px;
    padding: 0px 10px;
    color: #fff;
    float: left;
    border-radius: 5px;
    border:1px solid #fff
  }
  .selected{
    color: red;
    border:1px solid red

  }
  .warp{

    .no-message{
      text-align: center;
      font-size: 20px;
      color: #888;
    }
    .records{
      .records-item{
        margin-bottom: 20px;
        .records-date{
          border-bottom: 1px solid #ccc;
          margin:0px;
          padding-bottom: 10px;
        }
        .records-data{
          padding:6px 0px;
          border-bottom: 1px solid #ccc;
          .data,.date{
            height: 48px;
            p:first-child{
              font-size: 14px;
              color: #b3b3b3;
            }
            p{
              line-height: 24px;
              height: 24px;
              color: #323232;
              font-size: 18px;
              padding: 0px;
              margin: 0px;
            }
          }

          .data{
            .left,.right{
              height: 25px;

              width: 50%;
              float: left;
              .mint-badge{
                margin-top:4px;
                font-size: 12px;
              }
            }

            border-right: 1px solid #ccc;
          }
          .date{
            text-align: right
          }
        }
      }

    }
  }

}
</style>



