<template>
	<div class="create">
		<!-- <mt-header title="兴趣爱好" class="createName">
			<router-link to="/informate" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="msg">
			<div class="hobby">
				<h3>兴趣爱好</h3>
				<mytags :items="hobbys" :type="'check'" v-model="form.hobby_list"></mytags>
			</div>
			<div class="hobby">
				<h3>特长</h3>
				<mytags :items="specialtys" :type="'check'" v-model="form.speciality_list"></mytags>
			</div>
		</div>
		<div class="next">
			<router-link to="/job">
				<mt-button class="next-btn" @click="clickNext" :disabled="!isNext">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	import mytags from "../my-tags.vue"
	export default {
		components:{mytags},
		data (){
			this.getHobby();
			this.getSpecialty();
			const form = Object.assign(
				{
					hobby_list:"",
					speciality_list: ""
				},
				JSON.parse(window.localStorage.form)
			)
			return {
				form,
				hobbys:[],
		    	specialtys:[]
			}
		},
		computed: {
			isNext: function () {
				return (this.form.hobby_list!=''&&this.form.speciality_list!='')
			}
		},
		methods: {
			getHobby (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/hobbys");
			  resource.get().then((callback) => {
			  	callback.data.map(nav =>
						this.hobbys.push(
							{
								text:nav,
								value:nav
							}
						))
			  });
			},
			getSpecialty (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/specialitys");
				resource.get().then((callback) => {
					callback.data.map(nav =>
						this.specialtys.push(
							{
								text:nav,
								value:nav
							}
						))
				});
			},
			clickNext (){
				window.localStorage.form = JSON.stringify(this.form);
			}
    	}
	}
</script>
<style>
.hobby{
	margin-top: 10px;
	overflow: hidden;
}

.hobby h3{
	text-align: center;
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #ccc;
}
</style>



