<template>
	<div style="width: 99%;margin-left: 1%;">
		<!-- <mt-header fixed title="管理病例">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="/uploder" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25">
  		  </router-link>
  		</mt-header> -->
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  		<div style="margin:20px;">
			<div>
				<input type="search" class="search_ipt" v-model="searchName"><button @click="changeSearch(searchName)" class="sear_name">搜索</button>
			</div>
			<p><img src="../../assets/hot.png" alt=""style='vertical-align: middle;'>关键字</p>
			<div>
				<button v-for="button in buttons" class="btn_s" @click="changeButton(button.name)">{{button.name}}</button>
			</div>
			<!-- 新增按钮 -->
			<div class="next" style="margin-top: 15px;">
				<router-link to="/uploder" slot="right">
					<mt-button class="next-btn" @click="dialogVisible = true" style="border:1px solid #ad181c;background:#fff;color:#101010;"><span style="font-size:20px;color:#737373;">+</span>添加病例</mt-button>
  		  		</router-link>
			</div>
			<hr color="#e6e6e6">
			<div class="warp-scroll" id="dowebok" >
				<div v-for='group in groups' class="group_name" v-if="state">
					<div>{{group.record_date}}</div>
					<div class="item_name" >
						<div v-for='item in group.record_content' >
							<img v-bind:src="path+item.thumb_image_path"  @click="scale(path+item.image_path,item.id)">
							<div style="margin-top:5px;">{{item.name}}</div>
						</div>
					</div>
				</div>
				<div v-for='list in btnList || searchList' class="group_name" v-if="dispare">
					<div>{{list.record_date}}</div>
					<div class="item_name">
						<div v-for="item in list.record_content">
							<img v-bind:src="path+item.thumb_image_path" @click="scale(path+item.image_path,item.id)">
							<div style="margin-top:5px;">{{item.name}}</div>
						</div>
					</div>
				</div>
				<div v-if="nullData">
					<img src="../../assets/wenjian.png" style="width: 60%;margin: 19% 20% 5%;">
					<p style="color:#cccccc;font-size:14px;text-align:center;">您还没有添加任何病例，快来添加吧～</p>
				</div>
			</div>
			<!-- end -->
			<div class="next" style="margin-top:15px;" v-if="tag">
				<mt-button class="next-btn" @click="loadeMore()" style="border:1px solid #e6e6e6;background:#fff;color:#101010;">加载更多<img src="../../assets/down.png" style="vertical-align:middle;"></mt-button>
			</div>
		</div>
		<flexImg :picture="picture" :imgId="imgId" v-if="isClose" v-model="isClose"></flexImg>	
	</div>
</template>
<script>
import flexImg from "./flexImg"
import { Indicator } from 'mint-ui';
	export default{
		components:{flexImg},
		data(){
			var buttons = [{name:'体检报告'},{name:'CT'},{name:'B超'},{name:'血常规'}];
			this.getKill();
			return {
				searchName:'',
				value:null,
				buttons:buttons,
				sheetVisible: false,
				actions: [],
				groups:[],
				isClose:false,
				path: this.$store.state.serverJS,
				picture:'',
				imgId:'',
				searchList:[],
				btnList: '',
				state:true,
				page:1,
				tag:false,
				nullData:false
			}
		},
		computed:{
			dispare (){
				if(this.btnList.length <=0){

					return this.searchList.length<=0?false:true;
				}else{
					return this.btnList.length<=0?false:true;
				}

			}
		},
		methods:{
			getKill(){
				Indicator.open('加载中...');
				var page =1;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records?"+location.search.substring(1).split("&")[0]);
				resource.get({per_page:3,page:page}).then((response)=>{
					this.groups = response.data;
					if(this.groups.length==0){
						this.nullData=true;
					}else{
						this.nullData=false;
						this.tag = true;
					}
					Indicator.close();
				},(error)=>{

				})

			},
			loadeMore(){
				this.page +=1;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records?"+location.search.substring(1).split("&")[0]);
				resource.get({per_page:2,page:this.page}).then((response)=>{
					this.groups=this.groups.concat(response.data);
					if(response.data ==''){
						this.$toast('没有更多数据了');
						this.tag = false; 
					}else{
					}
				})
			},
			scale(url,id){
				this.isClose=true;
				this.picture = url;
				this.imgId = id;
				
			},
			//搜索框搜索
			changeSearch(search){
				this.state = false;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/search?"+location.search.substring(1).split("&")[0]);
				resource.get({name:search}).then((response)=>{
					this.searchList=response.data
					if(this.searchList==null){
						this.$toast('查无相关数据');
						this.getKill();
					}else{
						if(this.searchList.length<=0){
							this.tag = false; 
						}else{
							this.btnList='';
							this.tag = false; 
						}

					}
					if(search==""){
						this.searchList=[];
						this.btnList='';
						this.getKill();
						this.tag = true;
						this.state = true;
					}
				},(error)=>{
					this.$toast('查无相关数据');
				})
			},

			//按钮搜索
			changeButton(btn_name){
				this.isActive=true;
				this.btnList=''
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/search_two?"+location.search.substring(1).split("&")[0]);
				resource.get({name:btn_name}).then((response)=>{
					this.btnList=response.data
					if(this.btnList==null){
						this.$toast('查无相关数据');
						this.tag = false; 
					}else{
						if(this.btnList.length<=0){
							this.btnList=''
							this.$toast('查无相关数据');
							this.tag = false; 
						}else{
							this.state = false;
							this.tag = false; 
						}
					}
				},(error)=>{
					this.$toast('查无相关数据');
					this.tag = false; 
					this.state = false;
				})
			},
			
			// calce(){
			// 	document.getElementById("smark").style.display ="none";
			// }
		},
		
	}
</script>
<style scoped>
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 9vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s{
		margin-right: 3%;
		border-radius: 4px;
		background: #f5f5f5;
		color: #999;
		height: 41px;
		padding: 2% 5%;
		border: none;
	}
	.btn_s:hover{
		background: #ad181c;
		border: none;
		color: #fff;
	}
	.group_name{
		/*margin: 10px 10px;*/
        font-size: 20px;
        z-index: 1;
	}
	.item_name{
		font-size: 16px;
		margin-top: 12px;
	}
	.item_name div{
		display: inline-block;
		width: 22%;
		margin-right: 5px;
		line-height: 25px;
	}
	.item_name div>div{
		width:100%;
		line-height:17px;
		text-align:center;
	}
	.item_name img{
		width:100%;
		height:60px;
		border-radius:5px;
		/*border:1px solid #000;*/
	}
	.search_ipt{
		width:80%;
		background:#f0f0f0;
		display:inline-block;
		border: none;
		border-radius:4px;
		padding:8px;
		margin-right:12px;
		margin-right:6px;
		color: #323232;
	}
	#smark{
		background: rgba(0,0,0,.8);
		position: absolute;
		z-index: 99;
		display: none;
		top:0;
		left:0;
		bottom:0;
	}
	#smark>img{
		width: 50%;
		height: 50%;
		border: none;
		position: relative;
		margin-top: 22%;
	}
	.warp-scroll {
		overflow: auto;
		height:calc(100vh - 48vh)
	}
	.sear_name{
	    border: none;
	    font-weight: bold;
	    background: #ad181c;
	    color: #fff;
	    padding: 5px 2px 5px;
	    border-radius: 5px;
	    width: 17%;
	}
	
</style>


