<template>
	<div class="create">
		<!-- <mt-header title="家人" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
			<router-link to="/addCall" slot="right">
				<span><img src="../../assets/addicon.png" alt="add" width="20" height="20"></span>
			</router-link>
		</mt-header> -->
		<div class="focus">
			<div class="" style="background:#fcf4e0;padding:8px 10px;width:100%;"  data-clipboard-action="copy" data-clipboard-target="#code" id="btn">
				<i class="iconfont icon-yinhangqia" slot="icon" style="border-radius:50%;background:#5ac8ff;width:32px;height:32px;display:inline-block;text-align:center;line-height:32px;color:#fff;font-size:18px;"></i>
				<span>关注码:</span>
				<input type="text" name="" :value="code" id="code" style="background:transparent;border:0px;" readonly>
				<!-- <span >{{code}}</span> -->
			</div>
			<mt-cell v-for="item in focusForm" :title="item.whether==0?item.wx_name:item.appellation" v-if="item.whether!=2"  @click.native.stop="push(item)">
				<p v-if="item.whether==0">请求关注</p>
	  			<mt-button class="focus_btn" type="danger" size="small" color="#A21A28" v-if="item.whether==0" @click.native.stop="focusClick(item)" slot="right">关注</mt-button>
	  			<mt-button class="remove_btn" type="default" size="small" v-if="item.whether==0" slot="right" @click.native.stop="removeClick(item)">忽略</mt-button>
	  			<mt-button class="remove_btn" type="default" size="small" v-if="item.whether==1" slot="right" @click.native.stop="editClick(item)">编辑</mt-button>
	  			<span v-if="item.whether==1" slot="right">
	  				<i class="iconfont icon-taoxin"></i>
	  			</span>
	  			<img class="photo" slot="icon" :src="item.whether==0?item.follow_avatar:item.wx_avatar" v-if="item.follow_avatar || item.wx_avatar">
	  			<img class="photo" slot="icon" src="../../assets/photo.png" v-else>
			</mt-cell>
		</div>
		<div class="jiaren" v-show="jiaren">
		</div>
		<div class="tile" v-show="jiaren">
			<span>您还未建立自己的亲友圈，</span><br>
			<span>可点击添加按钮关注亲友～</span>
		</div>
		<div class="next save">
			<router-link to="/addCall">
				<mt-button class="next-btn">添加关注</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
import { Indicator } from 'mint-ui';
import { MessageBox } from 'mint-ui';
var Clipboard =  require('clipboard');
	export default {
		data (){
			this.getFocusForm();
			return {
				code:"",
				focusForm: [],
				jiaren: ''
			}
		},
		mounted () {
			var resource = this.$resource(this.$store.state.serverIP+'/users/get_code?'+location.search.substring(1).split("&")[0]);
			resource.get().then((response) => {
				this.code=response.data;
				//whether:处理结果0未处理1同意2忽略

			},(response) => {
				this.$toast(response.data.error);
			});
		},
		created () {
			var clipboard =new Clipboard('#btn');
			clipboard.on('success', function(e) {
				MessageBox({
					message: '验证码已复制，请发送给您关注的人！',
					confirmButtonText:'我知道了'
				});

			});
		},
		methods: {
			copy_code () {

				// const range = document.createRange();
        // range.selectNode(document.getElementById('code'));
        // const selection = window.getSelection();
        // if(selection.rangeCount > 0) selection.removeAllRanges();
        // selection.addRange(range);
        // document.execCommand('copy');

			},
			push(item){
				if(item.whether==1){
					this.$router.push({
						name:"familyData",
						params:{
							id:item.follow_id,
							title:item.appellation+'的健康数据'
						}
					})
				}
			},
			getFocusForm: function () {
				Indicator.open('正在加载');
				//关注请求列表接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/list_focu?'+location.search.substring(1).split("&")[0]);
				resource.get().then((response) => {
					this.focusForm=response.data;
					//whether:处理结果0未处理1同意2忽略
					this.jiaren = response.data.length == 0?true:false;
					Indicator.close();
				},(response) => {
					this.$toast(response.data.error);
				});
			},
			focusClick: function (item) {
				// 处理关注请求接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/problems_focu?'+location.search.substring(1).split("&")[0]);
				resource.save({focu_id:item.id,whether:1}).then((response) => {
					item.whether=1;
					item.wx_avatar=item.follow_avatar;
					item.appellation=item.wx_name;
				}, (response) => {
					this.$toast(response.data.error);
				})
			},
			removeClick: function (item) {
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/problems_focu?'+location.search.substring(1).split("&")[0]);
				resource.save({focu_id:item.id,whether:2}).then((response) => {
					item.whether=2;
				}, (response) => {
					this.$toast(response.data.error);
				});
			},
			editClick (item) {
				this.$router.push({
					name: 'editFocus',
					params:{
						focu_id:item.id,
						appellation:item.appellation
					}
				});
			}
		}
	}
</script>
<style lang="scss">
.focus{
	padding: 20px 10px;
	.mint-cell{
		border-bottom:1px solid #cccccc;
		.mint-cell-allow-right{
			display: none;
		}
		.mint-cell-title{
			span{
				display:inline-block;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				width:75%;
			}
		}
	}
	.icon-taoxin{
		color:#A21A28;
		font-size:20px;
	}
}
.focus .mint-cell-title{
	width: 190px !important;

}
.focus .mint-cell-right button{
	margin-left: 10px;
	height: 26px;
	padding: 0 9px;
	font-size: 13px;
}
.focus .mint-cell-value p{
	position: absolute;
	left: 45px;
	font-size: 14px;
	top: 20px;
}
.focus .mint-cell-right{
	right: 115px;
	top: 10px;
}
.focus .mint-cell-right .focus_btn{
	background-color: #A21A28;
}
.focus .mint-cell-right .remove_btn{
	background-color: #fff;
	border: 1px solid #000;
}
.focus .mint-cell-right span{
	position: absolute;
	right: -45px;
}
.photo{
	width: 32px;
	height: 32px;
	border-radius: 50%;
	background-color: #f0f0f0;
}
.jiaren{
	background: url("./../../assets/jiaren.png") center no-repeat;
	background-size: 95%;
	// background-size: cover;
	height: 100vw;
	width: 100%;
	margin: auto;
}
.tile {
	text-align:center;
	padding: 10px 10px;
	width:60%;
	font-size:16px !important;
	margin: auto;
	color: #ccc;
}
</style>



