<template>
	<div class="add-call">
		<!-- <mt-header title="家人" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div>
			<mt-field label="称呼" placeholder="请输入称呼" v-model="family.appellation" :attr="{ maxlength: 8 }" required></mt-field>
			<mt-field label="手机号" placeholder="请输入手机号码" v-model="family.tel" :attr="{ maxlength: 11 }" required></mt-field>
		</div>

		<div class="next">
			<mt-button class="next-btn" @click.native="submitForm">确定</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: '',
					tel: ''
				}
			}
		},
		methods:{
			submitForm () {
				if (this.family.appellation&&this.family.appellation!=''&&this.family.tel&&this.family.tel!='') {
					if ((/^1\d{10}$/).test(this.family.tel) == false) {
						this.$toast({
							message: '请输入正确的手机号码',
							position: 'bottom'
						});
					}else{
						//提交被关注人接口
						var resource = this.$resource(this.$store.state.serverIP+'/user_focus/add_focu?'+location.search.substring(1).split("&")[0]);
						resource.save(this.family).then((response) => {
		        			this.$toast({
		        				message: '提交成功',
		        				position: 'bottom'
		        			});
		        			setTimeout(() => {
		        				this.$router.go(-1);
		        			}, 1000);
						}, (response) => {
		     				this.$toast(response.data.error);
						})
					}
				}else{
					this.$toast({
						message: '请输入对方称呼或者手机号码',
						position: 'bottom'
					});
				}

			}
		}
	}
</script>
<style scoped>
.add-call{
	padding: 30px 20px;
	font-family: '微软雅黑';
}
</style>



