<template>
	<div style="width: 92%;margin-left: 3%;">
		<!-- <mt-header fixed title="服药提醒">
  		  <router-link to="/medicine" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25" @click="open('picker3')">
  		  </router-link>
  		</mt-header> -->
  		<div class="addMedicine">
  			<!-- 选择时间 start -->
		    <mt-datetime-picker ref="picker3" type="time" v-model="value3" @confirm="handleChange">
			</mt-datetime-picker>
  			<!-- 选择时间 end -->
  			<p style="font-weight: bold;margin-left: 10px">服用{{name}}提醒</p>
  			<el-row :gutter="20" style="margin:0;" v-for="item in times">
		        <el-col :span="19"><p>{{item.medicine_attention_time}}</p></el-col>
		            <el-col :span="5">
		              <p><el-button size="small" class="button_2" @click="movers(item.id)">删除</el-button></p>
		        </el-col>
		    </el-row>
		    <div v-if="nullData">
		        <img src="../../assets/time.png" style="width: 60%;margin: 19% 20% 5%;">
		        <div style="color:#e6e6e6;font-size:14px;text-align:center;">你还未设定提醒时间，</div>
		        <div style="color:#e6e6e6;font-size:14px;text-align:center;margin-bottom:30px;">可点击添加提醒时间～</div>
		    </div>
		    <div class="next" v-cloak>
				<mt-button class="next-btn" @click="open('picker3')">添加服药时间</mt-button>
			</div>
  		</div>
	</div>
</template>
<script>
	export default{
		data(){
	       	this.getMedicine();
	       	return {
	        	value3:new Date().getHours()+':'+ new Date().getMinutes(),
	        	name:this.$route.params.name,
	        	times:[],
	        	nullData:false
	       	}
		},
		methods:{
		  randomIndex: function () {
             return Math.floor(Math.random() * this.times.length);
          },
          //读取服药时间
          getMedicine(){
          	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/all_medicine_attentions?"+location.search.substring(1).split("&")[0]);
          	resource.get({take_medicine_management_id:this.$route.params.id}).then((response)=>{
          		this.times = response.data;
          		if(this.times.length==0){
          			this.nullData = true;
          		}else{
          			this.nullData = false;
          		}
          		console.log(this.times)
          	},(error)=>{

          	})
          },
          //删除某条服药时间
          movers(item){
          	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/delete_take_medicine_attention?"+location.search.substring(1).split("&")[0]);
          	resource.get({take_medicine_attention_id:item}).then((response)=>{
          		this.getMedicine();
          	},(error)=>{

          	})
          },
		 open(picker) {
	        this.$refs[picker].open();
	      },
	      //添加服药时间
	     handleChange(value) {
	     	this.value3=new Date().getHours()+':'+ new Date().getMinutes();
	     	if((this.times.length)<this.$route.params.usage){
		     	var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_attentions/add_take_medicine_attention?"+location.search.substring(1).split("&")[0]);
	          	resource.save({medicine_attention_time:value,take_medicine_management_id:this.$route.params.id}).then((response)=>{
	          		this.getMedicine();
	          	},(error)=>{

	          	})
	     	}else{
	     		this.$toast('已到药品用量上限');
	     		return
	     	}
	     	
	      }
		}
	}
</script>
<style  lang="scss">
	.mint-datetime{
		background: #f0f0f0;
	}
	.mint-datetime .picker-toolbar{
		position: absolute;
	    top: 200px;
	    width: 100%;
	    margin-bottom: 20px;
	}
	.mint-datetime-cancel{
		float: left;
	    width: 33%;
	    margin-left: 7%;
	    border: 1px solid #b3b3b3;
	    height: 33px;
	    line-height: 33px;
	    border-radius: 6px;
	    color: #A21A28;
	}
	.mint-datetime-confirm{
		width: 33%;
		margin-right: 7%;
	    border: 1px solid #b3b3b3;
	    height: 33px;
	    line-height: 33px;
	    border-radius: 6px;
	   background: #A21A28;
    	color: #fff;
}
.addMedicine{
	.picker-slot{
		font-size: 24px;
	}
	.picker-items{
		margin-bottom: 66px;
    	height: 175px;
	}
	.picker-item{
		top:-39px;
	}
	.picker {
	    .picker-item{
	      color:#b4b4b4;
	      font-weight: 100 !important;
	    }
	  .picker-item.picker-selected{
	    height: 50px;
	    color:#606060;

	    line-height: 50px;
	    font-size: 30px;
	  }
	    .picker-center-highlight{
	        height: 50px;
	    }
	    .picker-center-highlight:after,.picker-center-highlight:before{
	          background-color: #b3b3b3;
	    }
  	}
  }
</style>



