<template>
	<div class="create">
		<!-- <mt-header title="基本信息" class="createName">
			<router-link to="/register" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="msg">
			<mt-field label="姓名" placeholder="请输入姓名" v-model="form.truename"></mt-field>
			<mt-cell title="性别" class="informate">
				<el-radio class="radio" v-model="form.sex" label="1">男</el-radio>
				<el-radio class="radio" v-model="form.sex" label="2">女</el-radio>
			</mt-cell>
			<mt-cell title="年龄" class="age-input">
				<el-input type="number" v-model="form.age"></el-input>
				<span>岁</span>
			</mt-cell>
			<mt-field label="民族" placeholder="请输入民族" v-model="form.nation"></mt-field>
			<mt-cell title="证件类型" class="card-type">
				<el-select v-model="form.id_type" placeholder="请选择证件">
    				<el-option v-for="item in options" :label="item.label" :value="item.id_type">
    				</el-option>
  				</el-select>
			</mt-cell>
			<mt-cell title="证件号码">
				<el-input type="text" v-model="form.id_code" :maxlength="18" ></el-input>
			</mt-cell>
			<mt-cell title="血型" class="el-btn el-but">
				<mytags :items="blood_types" :type="'radio'" v-model="form.blood_type">
			  </mytags>
			</mt-cell>
			<mt-cell title="子女数" class="el-btn el-buts">
				<mytags :items="childrens" :type="'radio'" v-model="form.children">
			  </mytags>
			</mt-cell>
		</div>
		<div class="next">
			<mt-button class="next-btn" @click="clickNext" :disabled="!isNext">下一步</mt-button>
		</div>
	</div>
</template>
<script>
	import mytags from "../my-tags.vue"
	export default {
		components:{mytags},
		data (){
			const form = Object.assign(
				{
					truename:"",
					sex: '1',
					age:null,
					children:"",
					blood_type:"",
					id_code:null,
					id_type:"",
					nation:""
				},
				JSON.parse(window.localStorage.form)
			)
			return {
				form,
				childrens:[
					{
						text:"1",
						value:"1"
					},{
						text:"2",
						value:"2"
					},{
						text:"3",
						value:"3"
					},{
						text:"其他",
						value:"4"
					}],
				blood_types: [
					{
						value:"0",
						text:"A"
					},
					{
						value:"1",
						text:"B"
					},
					{
						value:"2",
						text:"AB"
					},
					{
						value:"3",
						text:"O"
					},
					{
						value:"4",
						text:"其他"
					}],
				options: [
					{
						id_type:0,
						label:'身份证'
					},
					{
						id_type:1,
						label:'护照'
					},
					{
						id_type:2,
						label:'军官证'
					},
					{
						id_type:3,
						label:'其他'
					}
				]
			}
		},
		computed: {
			isNext: function () {
				return (this.form.truename!=""&&
						this.form.sex!=''&&
						this.form.age!=null&&
						this.form.children!=''&&
						this.form.blood_type!=''&&
						this.form.id_code!=''&&
						this.form.id_type!=null&&
						this.form.nation!='')
			}
		},
		methods:{
			clickNext (){
				//判断必填
				if (!this.form.truename) {
					this.$toast('请输入姓名');
					return false
				}
				if (!this.form.age) {
					this.$toast('请输入年龄');
					return false
				}
				if (!this.form.id_code) {
					this.$toast('请输入证件号');
					return false
				}
		        if ((/[0-9]{17}[0-9Xx]{1}/).test(this.form.id_code) == false) {
		            this.$toast('请输入正确的身份证号');
		            return false;
		        }
				if (!this.form.blood_type) {
					this.$toast('请选择血型');
					return false
				}
				if (!this.form.children) {
					this.$toast('请选择子女数');
					return false
				}
				window.localStorage.form = JSON.stringify(this.form);
				this.$router.push({path:"/hobby"});

			}
		}
	}
</script>
<style scope>
.age-input .el-input{
	width: 25%;
	margin-right: 5px;
}
.age-input .el-input input{
	height: 30px;
}
.el-btn .el-button{
	padding: 5px 10px;
}
.mint-cell-title{
	width: 80px !important;
	flex: none !important;
}
.card-type .el-input{
	width:88%;
}
</style>



