<template>
  <div class="health-items">
    <el-row v-for="(items,row) in healthList" :class="{'items-row':row!=healthList.length-1}">
      <el-col :span="12" v-for="(item,col) in items" :class="{'items-col':col==0}">
        <!-- <router-link :to="{name:'itemDetails',params:{id:item.id,title:item.name}}" style="text-decoration:none;"> -->
          <div class="cards iconfont" @click="itemDetails(item)">
              <span class="card-icon" :class="item.icon||'icon-yanjing'" :style="{background:item.icon_bgcolor||'#ff9600'}"></span>
              <p v-text="item.name"></p>
          </div>

      </el-col>
    </el-row>
     <!-- 添加新数据弹框 -->
          <el-dialog title="添加关注项目" v-model="dialogVisible" size="tiny" :close-on-click-modal="false">
          <el-form :model="dataForm" v-if="dialogVisible" :rules="rules" ref="dataForm" class="dia_span">
          <el-form-item prop="name" >
            <div class="el-form-item__content">
              <span>名称</span>
              <!-- <el-input v-model="dataForm.name" :maxlength='10' v-if="is_admin==1" disabled="disabled"></el-input> -->
              <el-input v-model="dataForm.name" :maxlength='10'></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="unit" >
            <div class="el-form-item__content">
              <span>单位</span>
              <!-- <el-input v-model="dataForm.unit" :maxlength='10' v-if="is_admin==1" disabled="disabled"></el-input> -->
              <el-input v-model="dataForm.unit" :maxlength='10'></el-input>
            </div>
          </el-form-item>
          <el-form-item prop="normal_max">
            <div class="el-form-item__content">
              <span>正常值</span>
              <el-input :maxlength='10' v-model="dataForm.normal_min"style="width:25%;margin-right:6px;"></el-input>～
              <el-input :maxlength='10' v-model="dataForm.normal_max"style="width:25%;margin-left:6px;"></el-input>
            </div>
          </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false" style="border-color:#A21A28;">取 消</el-button>
            <el-button type="primary"  @click="submit('dataForm')"class="tan_name">确 定</el-button>
            <!-- <el-button type="primary"  @click="tijiao()" v-else class="tan_name">确 定</el-button> -->
          </span>
        </el-dialog>
        <!-- 添加新数据弹框 end-->
        <!-- </router-link> -->
  </div>

</template>

<script>
import { Indicator } from 'mint-ui';
export default {
  data (){
    return {
        healthList:[],
        dialogVisible: false,
        // dataForm:{},
        dataForm:{
            name:"",
            unit:"",
            normal_max:""
          },
          rules:{
            name: [
                    { required: true, message: '请输入关注项目名称', trigger: 'blur' }
                  ],
              unit: [
                { required: true, message: '请输入关注项目单位', trigger: 'blur' }
              ],
              normal_max: [
                { required: true, message: '请输入关注项目的正常范围', trigger: 'blur' }
              ]
          }
    }
  },
  methods:{
    getHealthitem (){
      // this.healthList=[];
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?status=1&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        try {
          if(callback.data.length>0){
            var list=[];
            for(var chr of callback.data){
              list.push(chr);
              if(list.length>=2){
                this.healthList.push(list);
                list = [];
              }

            }
            list.push({
              icon:"icon-plus",
              icon_bgcolor:"#ccc",
              type:"add-item"
            })
            this.healthList.push(list);

          }else{
            this.$toast('暂无数据');
          }
        }catch(error){
          this.$toast(error);
        }
        Indicator.close();
      },error=>{
        this.$toast(error);
        Indicator.close();
      });
    },
    submit(formName){//弹框的提交
        // this.dataForm.user_id = 2;
        this.$refs[formName].validate((valid) => {
                if (valid) {
                  // alert('submit!');
                  var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_health_item?"+location.search.substring(1).split("&")[0]);
                  resource.save(this.dataForm).then((response)=>{
                    this.dialogVisible = false;
                    if(this.healthList[this.healthList.length-1].length==1){
                      this.healthList[this.healthList.length-1].splice(0,1);
                      this.healthList[this.healthList.length-1].push(response.data.health_item);
                      this.healthList[this.healthList.length-1].push({
                        icon:"icon-plus",
                        icon_bgcolor:"#ccc",
                        type:"add-item"
                      })
                    }else{
                      this.healthList[this.healthList.length-1].splice(1,1);
                      this.healthList[this.healthList.length-1].push(response.data.health_item);
                       this.healthList.push([{
                        icon:"icon-plus",
                        icon_bgcolor:"#ccc",
                        type:"add-item"
                      }
                        ])
                    }
                    this.dataForm = {};
                  },(error)=>{

                    this.$toast({
                      position: 'bottom',
                      duration: 3000,
                      message:error.data.error
                    });
                  })
                } else {
                  // console.log('error submit!!');
                  // return false;
                }
              });

      },
    resetForm(formName) {
           this.$refs[formName].resetFields();
         },
    itemDetails (item){
      if(item.type&&item.type=="add-item"){
        // this.$router.push({path:"/healthData"});
        this.dialogVisible = true;
        return false
      }
      try {
        if(item.subitems>0){
          var flag = true;
          for(var chr of item.subitems) {
            if(chr.sub_value_range==null){
              //去输入框
              this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
              flag = false;
              break;
            }else{
              try {
                var list = chr.sub_value_range.split(",");
                if(list.length!=2){
                  this.$toast("数据错误，请输入选项值");
                  // 去输入框
                  this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
                  flag = false;
                  break;
                }
              } catch (e) {
                  this.$toast("数据错误，请输入选项值");
                  // 去输入框
                  this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
                  flag = false;
                  break;
              }
            }


          }
          if(flag){
            //去选择框
            this.$router.push({name:'itemSelect',params:{id:item.id,title:item.name}});
          }
        }else{
          if(item.value_range==null){
            //去输入框
            this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
          }else{
            try {
              var list = item.value_range.split(",");
              if(list.length!=2){
                this.$toast("数据错误，请输入选项值");
                // 去输入框
                this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});

              }else{
                //去选择框
                this.$router.push({name:'itemSelect',params:{id:item.id,title:item.name}});
              }
            } catch (e) {
                this.$toast("数据错误，请输入选项值");
                // 去输入框
                this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});

            }
          }
        }
      } catch (e) {
        this.$toast(e);
      }
    }
  },
  mounted (){
    Indicator.open('加载中...');
    this.getHealthitem();
  }
}

</script>

<style scoped lang="scss">
.health-items{
  .items-row{
    border-bottom:1px solid #e5e5e5;

  }
  .items-col{
    border-right:1px solid #e5e5e5;
  }
  .cards {
    span{
      display: block;
      margin: 20px auto;
      border-radius: 50%;
      height: 70px;
      line-height: 70px;
      text-align: center;
      color: #fff;
      font-size: 44px;
      width: 70px;
    }
    p{
      text-align: center;
      color: #323232;
    }
  }
}

</style>



