<template>
	<div class="smark">
		<div id="warp_img" >
			<img class="picture" :src="picture"   @click.stop="" id="img" >
			<img  class="button-back"  src="../../assets/back.png"   @click="close">
			<img  class="button-delete"  src="../../assets/move.png"  @click="move(imgId)">
		</div>
		
	</div>
</template >
<script >
import { Indicator } from 'mint-ui';
	export default {
		props:{
			picture:String,
			value:Boolean,
			imgId:Number
		},
		
		methods:{
			close(){
				this.$emit('input', false)
			},
			
			updateImg(){
				var warp_img= document.getElementById('warp_img');
				var img= document.getElementById('img');

				//判断横竖
				if(img.offsetWidth>=img.offsetHeight){
					//横图缩短宽度
					img.style.width = warp_img.offsetWidth+"px";

				}else{
					//竖图缩短高度
					img.style.height = (warp_img.offsetHeight-200)+"px";
				}
				//居中显示
				//上下
				img.style.top = (warp_img.offsetHeight - img.offsetHeight)/2 + "px";
				// 左右
				img.style.left = (warp_img.offsetWidth - img.offsetWidth)/2 + "px";
				img.setAttribute("class","fadein")
			},
			 imgLoad(img, callback) {
	            var timer = setInterval(function() {
	                if (img.complete) {
	                    callback(img)
	                    clearInterval(timer)
	                }
	            }, 50)
        	},
        	move(imgId){
        		var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/delete_medical_record_management?"+location.search.substring(1).split("&")[0]);
        		this.$confirm('您确定要删除吗?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(()=>{
	        		resource.get({medical_record_management_id:imgId}).then((response)=>{
	        			this.close();
	        			location.reload();
	        			// console.log(response.data)
	        		},(error)=>{

	        		})
		        })
        	}
       
		},
		
		mounted (){
			Indicator.open('加载中...');
			this.imgLoad(document.getElementById('img'),(img)=>{
				this.updateImg();
				Indicator.close();
			})
			
			
		}
	}
</script>
<style  lang="scss">
	.smark{
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		position: fixed;
		background: rgba(0,0,0,.8);
		z-index: 99;
		
		#warp_img{
			width: 100%;
			height:100%;
			position:relative;
			img{
				display: block;
				position:absolute;
			}
			.picture{
				opacity: 0;
				transform: scale(0);
				border:1px solid #fff;
				transition: all 0.8s;
				-moz-transition: all 0.8s;	/* Firefox 4 */
				-webkit-transition: all 0.8s;	/* Safari 和 Chrome */
				-o-transition: all 0.8s;
			}
			.button-delete{
				border:none;
			    bottom: 6%;
			    left: 67%;
			}
			.button-back{
				border:none;
				left:21%;
			    bottom: 6%;
			}
			.fadein{
				transform: scale(1);
				opacity: 1
			}
				
		}
		
	}
	.mint-indicator-wrapper{
		z-index: 999;
	}
	.smark img{
		
	}
	// .el-button--primary{
	// 	color: #fff;
	// }
	// .el-button--primary:active,.el-button--primary:hover{
	// 	color: #fff;
	// }
	

</style>


