<template>
  <div style="padding:20px 16px;">
    <el-row class="card" v-for="item in items" v-cloak>
      <el-col :span="8" class="label" style="text-align:right">{{item.name}} </el-col>
      <el-col :span="12" class="value" style="text-align:left">
        <input class="input" type="number" v-model="item.value">
      </el-col>
      <el-col :span="4" style="text-align:left;height:70px;line-height:70px">
        <span>{{item.sub_unit ||item.unit}}</span>
      </el-col>
    </el-row>
  <!-- <el-input type="textarea" disabled="disabled"></el-input> -->
  <mt-button class="submit" @click.native="submit">保存</mt-button>
  </div>


</template>
<script>
import { Indicator } from 'mint-ui';

export default {
  data() {
    return {
      items:[],
      form:{}
    };
  },
  methods: {

    checkForm (){
      if(this.items.length>0){
        var list=[];
        for(var ins of this.items){
          list.push(ins.value)
          if(ins.value==""){
            this.$toast("请输入"+ins.name);
            return true;
            break;
          }
        }
        this.form.content=list.join(",")||"";
        this.form.health_item_id=this.$route.params.id;
        return false;
      }else{
        return true
      }

    },

    submit (){
      if(this.checkForm())return false;
      Indicator.open("正在保存...");
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_item_record");
      resource.save(this.form).then(callback => {
          Indicator.close();
          this.$toast("保存成功");
          setTimeout(()=>{
            this.$router.go(-1);
          },1000)
      },error=>{
        Indicator.close();
        this.$toast(error);
        setTimeout(()=>{
          this.$router.go(-1);
        },1000)
      });
    },
  },
  mounted () {
    Indicator.open('正在加载...');
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/health_item?health_item_id="+this.$route.params.id+"&"+location.search.substring(1).split("&")[0]);
      resource.get().then(callback => {
        if(callback.data.subitems.length<=0){
          callback.data.value="";
          this.items.push(callback.data);
        }else{
          this.items = callback.data.subitems.map(nav=>{
            nav.value="";
          })
        }
        Indicator.close();
      },error=>{
        Indicator.close();
        this.$toast(error);
        this.$router.go(-1);
      });
  }

}
</script>

<style   lang="scss">
.card{
  text-align: center;
  overflow: hidden;
  margin: 0px auto;
  margin-bottom: 30px;
  .label,.value {
    height: 70px;
    color: #323232;
    line-height: 70px;
  }
  .value .input{
      text-align: center;
      border: 1px solid #ccc;
      width: 120px;
      font-weight: 100;
      color: #b01421;
      font-size: 50px;
      height: 66px;
      line-height: 66px;
      margin-left: 10px;
      border-radius: 4px;
      outline: none;
  }
}

.submit{
	background-color: #A21A28;
	outline: none;
	color: #fff;
	width: 100%;
  margin: 20px auto;
	font-family: '微软雅黑';
}
</style>



