<template>
	<div class="member" v-cloak>
		<div class="mem-cainter">
	  		<div style="background: #DA2905;padding:30px 0px;">
	  			<div class="user-photo">
					<img :src="informateList.wx_avatar" id="img" slot="center" v-if="isPhoto">
					<img src="../../assets/photo.png" slot="center" v-else>
				</div>
				<div class="user-name">
					<p>{{informateList.truename}}</p>
				</div>
				<ul class="inf-u">
					<li>
						<p>{{informateList.sex | sexFilter}}</p>
						<span>性别</span>
					</li>
					<li>
						<p>{{informateList.age}}</p>
						<span>年龄</span>
					</li>
					<li>
						<p>{{informateList.blood_type | blood_typeFilter}}</p>
						<span>血型</span>
					</li>
				</ul>
	  		</div>
			<div class="mem-background"></div>
			<div class="info-el">
				<el-row>
	  				<el-col :span="8">证件类型</el-col>
	  				<el-col :span="12">{{informateList.id_type |id_typeFilter}}</el-col>
				</el-row>
				<el-row>
	  				<el-col :span="8">证件号码</el-col>
	  				<el-col :span="12">{{informateList.id_code}}</el-col>
				</el-row>
				<el-row>
	  				<el-col :span="8">子女数</el-col>
	  				<el-col :span="12">{{informateList.children | childrenFilter}}</el-col>
				</el-row>
				<el-row>
	  				<el-col :span="8">职业</el-col>
	  				<el-col :span="12">{{informateList.job_list}}</el-col>
				</el-row>
		        <el-row>
	  				<el-col :span="8">兴趣爱好</el-col>
	  				<el-col :span="12">{{informateList.hobby_list}}</el-col>
				</el-row>
				<el-row>
	  				<el-col :span="8">特长</el-col>
	  				<el-col :span="12">{{informateList.speciality_list}}</el-col>
				</el-row>
			</div>
			<div class="next save">
				<mt-button class="next-btn" @click="editClick">编辑</mt-button>
			</div>
		</div>
  	</div>
</template>

<script>
export default {
	data () {
		return {
			isPhoto:true,
			memberList:{
				user_vip_info:{}
			}
		}
	},
	created (){
		this.informateList = JSON.parse(window.localStorage.memberList);
		console.log(this.informateList);
	},
	filters: {
		sexFilter:function (input) {
			switch(input){
				case 1: return '女';
				case 0: return '男';
			}
		},
		blood_typeFilter:function (input) {
			switch(input){
				case 0: return 'A型';
				case 1: return 'B型';
				case 2: return 'AB型';
				case 3: return 'O型';
				case 4: return '其他';
			}
		},
		id_typeFilter:function (input) {
			switch(input){
				case 0: return '身份证';
				case 1: return '护照';
				case 2: return '军官证';
				case 3: return '其他';
			}
		},
		childrenFilter:function (input) {
			switch(input){
				case 1: return '1';
				case 2: return '2';
				case 3: return '3';
				case 4: return '其他';
			}
		}
	},
	methods: {
		editClick (){
			window.localStorage.informateList = JSON.stringify(this.informateList);
			this.$router.push('/editInfo');
		}
	}
}
</script>

<style scoped lang="scss">
.member .mem-cainter{
	width: 100%;
	overflow: hidden;
	color: #fff;
	font-family: "微软雅黑";
	font-size: 16px;
	margin: 0;
  .user-name{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .user-photo{
    margin: 0px auto;
    height: 65px;
    width: 65px;
    border-radius: 50%;
   	background-color:#f0f0f0;
    overflow: hidden;
    img{
      width: 65px;
      height: 65px;
    }
  }
  .user_card{
  	margin:auto;
  	overflow:hidden;
  	padding-left:30px;
  	p {
  		margin:0px;
  		float:left;
  		padding-left:10px;
  		height:74px;
  		line-height:74px;
  	}
  },
  .inf-u{
  	overflow:hidden;
  	margin:0;
  	padding:0;
	  	li{
			float:left;
			text-align:center;
			list-style-type:none;
			width:33%;
			p{
				font-size:17px;
			}
			span{
				font-size:15px;
			}
		}
  },
  .info-el{
		.el-row{
			padding:10px 20px;
			font-size:16px;
			color: #323232;
		}
	}
}
.mem-background{
	height: 55px;
	background:url("./../../assets/backred_text.png") no-repeat;
	background-size: 100%;
	margin: 0;
	margin-top: -1px;
}
</style>



