<template>
  <ul class="my-tags">
    <!-- <slot name="item"   @click="updateValue(item.value)"></slot> -->
    <li :class="{ 'is-selected': list.indexOf(item.value)>=0}" v-for="item in items" @click="updateValue(item.value)">{{item.text}}</li>

  </ul>
</template>
<script>
  export default {
    props:{
      type:String,
      items:Array,
      value:String
    },
    data (){
      return {
        list:[]
      }
    },
    mounted (){
      this.list = this.value.split(',');
      if(this.list.length === 1&& this.list[0] == ""){
        this.list = [];
      }
    },
    methods:{
      updateValue (value){

        if(this.type=="radio"){
          this.list = []
          this.list.push(value);
        }else{
          if(this.list.indexOf(value)>=0){
            const index = this.list.indexOf(value);
            this.list.splice(index,1);
          }else{
            this.list.push(value);
          }
        }
        this.$emit('input', this.list.join(','));

      }
    }
  }
</script>
<style>
.my-tags{
  padding: 0;
  list-style: none;
}
.my-tags li{
  float: left;
  min-width: 25px;
  text-align: center;
  padding: 5px 8px;
  font-size: 14px;
  color: #ccc;
  border: 1px solid #ccc;
  margin: 5px;
  margin-left: 0px;
  border-radius: 5px;
}
.my-tags li.is-selected {
  color:#b01421;
  border-color:#b01421;
}
</style>



