    }
  },
  created (){
    var resource = this.$resource(this.$store.state.serverIP+'/user_focus/list_focu?'+location.search.substring(1).split("&")[0]);
    resource.get().then((response) => {
      this.focusForm=response.data;
      //whether:处理结果0未处理1同意2忽略
      if(response.data.length>0){
        for (var resource of response.data){

          if(resource.whether==0){
            this.isHasFocus=false;
            break;
          }
        }
      }


    },(response) => {
      this.$toast(response.data.error);
    });
  }

}
</script>

<style lang="scss" scoped>
.my-tabs{
  /*margin-top: 40px;*/
  list-style: none;
  padding: 0px;
  // overflow: hidden;
  height: 46px;
  line-height: 46px;
  margin-left: 2.5%;
  width: 95%;
  margin-bottom: 0px;
  li{
    color:#323232;
    width:calc(50% - 1px);
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    margin-top: 2px;
    text-align: center;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    float: left;
  }
  .tab:first-child li{
    border-left: 1px solid #ccc;
