  width: 50%;
}
  .menu{
    width: 98%;
    border: 1px solid #ddd;
    background: #fff;
    position: absolute;
    bottom: 65px;
    border-radius: 2px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    padding: 0;
  }
  .menu li{
    list-style-type:none;
    text-align: center;
    border-bottom: 1px solid rgba(204,204,204,.3);
    height: 35px;
