<template>
	<div class="create">
		<mt-header title="修改密码" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="msg">
			<mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-model="form.username"></mt-field>
			<mt-field label="验证码" placeholder="请输入验证码" type="number" class="msg-btn" v-model="code">
  				<el-button type="primary" :loading="isCodeLoading"  :disabled="wait>0" class="click-btn"  @click="getCode">{{CodeMsg}}</el-button>
			</mt-field>
		</div>
		<div class="next">
			<router-link to="/setPassword">
			    <mt-button class="next-btn" @click.native="clickNext" :disabled="!isNext">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				form: {
					username: '',
					code:this.$route.query.code || ""
				},
				isCodeLoading:false,
				code: '',
				wait:0,
				CodeMsg:'点击获取',
				confirm_code:'',
				confirm_password: ''
			}
		},
		computed:{
			checkCode:function() {
				return (this.code!=''&&this.code == this.confirm_code)
			},
			isNext:function(){
				return (this.form.username!=''&&(this.code!=''&&this.code == this.confirm_code))
			}
		},
		methods: {
			getCode (){
					this.CodeMsg='获取验证码';
					this.isCodeLoading = true;
					setTimeout(()=>{
						this.isCodeLoading = false;
						this.confirm_code = 12345;
						this.wait = 60;
						this.CodeMsg= this.wait+'s后获取';
						var codeTime = setInterval(()=>{
							this.wait--;
							this.CodeMsg= this.wait+'s后获取';

							if(this.wait<=0){
								clearInterval(codeTime);
								this.CodeMsg = "重新获取";
							}
						},1000)
					},2000)
			},
			clickNext (){
				window.localStorage.form = JSON.stringify(this.form);
			}
		}
	}
</script>
<style>


</style>



