<template>
	<div style="width: 92%;margin-left: 3%;">
		<!-- <mt-header fixed title="服药管理">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25" @click="dialogVisible = true">
  		  </router-link>
  		</mt-header> -->
  		<div>
  			<!-- 弹框内容 -->
  			<el-dialog title="新增药品" v-model="dialogVisible" size="tiny" :close-on-click-modal="false">
  			<el-form :model="form" v-if="dialogVisible" :rules="rules" ref="form" class="dia_span">
				  <el-form-item prop="name" >
				   		<span>药品名称</span>
				   		<el-input v-model="form.name" :maxlength='10'></el-input>
				  </el-form-item>
				   <el-form-item prop="usage" class="dia_ipt">
				   		<span>用法</span>
				   		<el-input v-model="form.usage" :maxlength='1'></el-input>
				   		次/日
				  </el-form-item>
				  <el-form-item prop="dosage" class="dia_ipt">
					   <span>用量</span>
					   <el-input v-model="form.dosage" :maxlength='1'></el-input>片/次
				  </el-form-item>
			  </el-form>
				  <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogVisible = false" style="border-color:#A21A28;">取 消</el-button>
				    <el-button type="primary"  @click="submit('form')" class="tan_name">确 定</el-button>
				  </span>
			</el-dialog>
  			<!-- 弹框内容 end  -->
  			
  			<div class="medic_name" v-for='item in medicines'>
  				<span class='a'>{{item.name}}</span>
  				<span style="width:18%;">{{item.usage}}次/日</span>
  				<span style="width:18%;">{{item.dosage}}片/次</span>
  				<span>
  					<router-link :to="{name:'addMedicine',params:{id:item.id,name:item.name,usage:item.usage}}">
  						<el-button size="small" class="button_1">提醒</el-button>
  					</router-link>
  				</span>
  				<span>
  					<!-- <router-link :to="{name:'addMedicine',params:{id:item.id,name:item.name,usage:item.usage}}"> -->
  						<el-button size="small" class="button_1" @click="movers(item.id)">删除</el-button>
  					<!-- </router-link> -->
  				</span>
  			</div>
  			<div v-if="nullData">
  				<img src="../../assets/yao.png" style="width: 60%;margin: 19% 20% 5%;">
	         	<div style="color:#cccccc;font-size:14px;text-align:center;">你还没有添加任何药品项，</div>
          		<div style="color:#cccccc;font-size:14px;text-align:center;margin-bottom:30px;">可点击添加帮助您管理日常药物～</div>
  			</div>
  			<div class="next" v-cloak style="margin-top:20px;">
				<mt-button class="next-btn" @click="dialogVisible = true">添加服药项目</mt-button>
			</div>
  		</div>
	</div>
</template>
<script>
import { Indicator } from 'mint-ui';

	export default{
		data(){
			this.getSubmit();
			return{
				dialogVisible:false,
				medicines:[],
				form:{},
				nullData:false,
				ruleForm:{
					name:"",
					usage:"",
					dosage:""
				},
				rules:{
					name: [
			            { required: true, message: '请输入药品名称', trigger: 'blur' }
			          ],
			          usage: [
			            { required: true, message: '请输入每日药品服用的次数', trigger: 'blur' }
			          ],
			          dosage: [
			            { required: true, message: '请输入每次药品服用的剂量', trigger: 'blur' }
			          ]
				}
			}
		},
		methods:{
			//读取数据列表
			getSubmit(){
				 Indicator.open('加载中...');
				var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_managements/all_take_medicines?"+location.search.substring(1).split("&")[0]);
		    	resource.get({}).then((response)=>{
		    		Indicator.close();
		    		this.medicines = response.data;
		    		if(this.medicines.length==0){
		    			this.nullData=true;
		    		}else{
		    			this.nullData=false;
		    		}
		    	},(error)=>{

		    	})
			},
			//提交添加新数据
			submit(formName){
				// location.search.substring(1).split("&")[0]
				// 解析出来后: wx_id=111111111
				// debugger
				// this.form.user_id = 2;
				var resource = this.$resource(this.$store.state.serverIP+'/take_medicine_managements/add_take_medicine_management?'+location.search.substring(1).split("&")[0]);
				this.$refs[formName].validate((valid) => {
			          if (valid) {
			            // alert('submit!');
			          } else {
			            // console.log('error submit!!');
			            // return false;
			          }
			        });
					resource.save(this.form).then((response)=>{
						this.getSubmit()
						this.dialogVisible = false;
						
						this.form={}
					},(error)=>{

					})
			},
			resetForm(formName) {
			       this.$refs[formName].resetFields();
			     },
			movers(item){
	    		var resource = this.$resource(this.$store.state.serverIP+"/take_medicine_managements/delete_take_medicine_management?"+location.search.substring(1).split("&")[0]);
	    		this.$confirm('删除本项数据可能会造成相关联数据的丢失, 您确定删除吗?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		    		resource.get({take_medicine_management_id:item}).then((response)=>{
						this.getSubmit();
		    		},(error)=>{
		    		})
		        })
	    	}
		}
	}
</script>
<style lang="scss">
	.el-dialog--tiny{
		width: 90%;
	}
	.dialog_s input{
		border:1px solid #8492a6;
		margin-bottom: 20px;
		height: 30px;
		border-radius: 5px;
		margin-right: 12px;
	}
	.el-dialog__header{
		text-align: center;
	}
	.el-dialog__footer{
		text-align: center;
	}
	.medic_name{
		margin-left:5px;
		border-bottom:1px solid rgb(234, 234, 234);
		padding:15px 0px;
		width: 100%;
	}
	.medic_name>span{
		display: inline-block;
	}
	.medic_name .a{
		width:25%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		vertical-align: text-bottom;
	}
	.dia_span span{
		display: inline-block;
		width: 25%;
	}
	.dia_span{
		.el-input{
			width: 60%;
		}
	}
	.dia_ipt{
		.el-input{
			width: 30%;
		}
	}
</style>


