<template>
	<div class="create">
		<!-- <mt-header title="注册" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="start">
			<img class="memberList_wx_avatar" :src="memberList.wx_avatar" v-if="memberList.wx_avatar">
			<img class="memberList_photo" src="../../assets/photo.png" v-else>
		</div>
		<div class="next start-btn">
			<router-link to="/register">
				<mt-button class="next-btn">立即注册</mt-button>
			</router-link>
		</div>
	</div>
</template>

<script>
	export default {
		data (){
			return {
				memberList: {}
			}
		},
		methods: {
			getMessage () {
				//获取用户信息
				var resource = this.$resource(this.$store.state.serverIP+'/users/get_user_info/?'+location.search.substring(1).split("&")[0]);
				resource.get().then((response) => {
					this.memberList = response.data;
				},(response) => {
					this.$toast({
        				message: '提交失败',
        				position: 'bottom'
        			});
				});
			}
		},
		mounted (){
			this.getMessage();
		}
	}
</script>

<style lang="scss">
.start{
	width: 70px;
	height: 70px;
	margin: 0 auto;
	padding: 10px;
	margin-top: 100px;
	margin-bottom: 140px;
	border-radius: 50%;
	.memberList_wx_avatar,.memberList_photo{
		width: 70px;
		height: 70px;
		border-radius:50%;
	}
	.memberList_photo{
		background-color:#f0f0f0;
	}
}
</style>



