<template>
	  <div>
	  	<div style="width: 92%;margin-left: 3%;">
	  		<!-- <mt-header fixed title="健康数据">
	  		  <router-link to="/" slot="left">
	  		    <mt-button icon="back"></mt-button>
	  		  </router-link>
	  		  <router-link to="" slot="right">
	  			<img src="../../assets/addicon.png" width="25" height="25" @click="dialogVisible = true">
	  		  </router-link>
	  		</mt-header> -->
	  		<div >
	  			<!-- 弹框 添加新项目 -->
		  		<el-dialog v-model="dialogVisible" size="tiny" :close-on-click-modal="false">
		  		<el-form :model="dataForm" v-if="dialogVisible" :rules="rules" ref="dataForm" class="dia_span">
	  			  <el-row :gutter="20" style="padding-left:13%;"><el-col :span="24" class="title_name">{{Newtitle}}</el-col></el-row>
				  <el-form-item prop="name" >
				  	<div class="el-form-item__content">
				   		<span>名称</span>
				   		<el-input v-model="dataForm.name" :maxlength='10' v-if="is_admin==1" disabled="disabled"></el-input>
				   		<el-input v-model="dataForm.name" :maxlength='10' v-else></el-input>
				  	</div>
				  </el-form-item>
				  <el-form-item prop="unit" >
				  	<div class="el-form-item__content">
				   		<span>单位</span>
				   		<el-input v-model="dataForm.unit" :maxlength='10' v-if="is_admin==1" disabled="disabled"></el-input>
				   		<el-input v-model="dataForm.unit" :maxlength='10' v-else></el-input>
				  	</div>
				  </el-form-item>
				  <el-form-item prop="normal_max">
				  	<div class="el-form-item__content">
				   		<span>正常值</span>
				   		<el-input :maxlength='10' v-model="dataForm.normal_min"style="width:25%;margin-right:6px;"></el-input>～
				   		<el-input :maxlength='10' v-model="dataForm.normal_max"style="width:25%;margin-left:6px;"></el-input>
				  	</div>
				  </el-form-item>
				  </el-form>
				  <span slot="footer" class="dialog-footer">
				    <el-button @click="dialogVisible = false" style="border-color:#A21A28;">取 消</el-button>
				    <el-button type="primary"  @click="submit('dataForm')"v-if='state' class="tan_name">确 定</el-button>
				    <el-button type="primary"  @click="tijiao()" v-else class="tan_name">确 定</el-button>
				  </span>
				</el-dialog>
	  			<!-- end -->
		  		<el-row :gutter="20" class="an_niu" style="border-bottom: 1px solid #EAEAEA;" v-for="data in healthDatas">
		  		  <el-col :span="9"><p @click="eadit(data)" style="width:100%;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;vertical-align: text-bottom;">{{data.name}}</p></el-col>
		  		  <el-col :span="5" v-if="data.is_admin==1">
		  		  	<p></p>
		  		  </el-col>
		  		  <el-col :span="5" v-if="data.is_admin==0">
		  		  	<p><el-button size="small" class="button_2" @click="movers(data.id)">删除</el-button></p>
		  		  </el-col>
		  		  <el-col :span="5">
			  		  <router-link :to="{name:'pathrRemind',params:{id:data.id,title:data.name}}">
			  		  	<p v-if="data.is_check==true"><el-button size="small" class="button_1">提醒</el-button></p>
			  		  </router-link>
			  		  <p v-if="data.is_check==false"><el-button size="small" class="button_1"style="color:#b3b3b3;border-color:#b3b3b3;">提醒</el-button></p>
		  		  </el-col>
		  		   <el-col :span="5">
		  		  	<p><el-switch
	  		  		  on-color="#A21A28" off-color="#C0CCDA" on-text="" off-text="" @change="getCheck(data)" v-model="data.is_check">
	  		  		</el-switch></p>
		  		  </el-col>
		  		</el-row>
		  		<div class="next" v-cloak style="margin-top:20px;">
					<mt-button class="next-btn" @click="guanzu()">添加关注项目</mt-button>
				</div>
	  		</div>
	  	</div>
	  </div>
</template>
<script>
import { Indicator } from 'mint-ui';
	export default {
	    data() {
		  this.getdata();
	      return {
	        healthDatas:'',
	        dialogVisible: false,
	        dataForm:{},
	        state:true,
	        is_admin:'',
	        Newtitle:'',
        	ruleForm:{
        		name:"",
        		unit:"",
        		normal_max:""
        	},
        	rules:{
        		name: [
                    { required: true, message: '请输入关注项目名称', trigger: 'blur' }
                  ],
              unit: [
                { required: true, message: '请输入关注项目单位', trigger: 'blur' }
              ],
              normal_max: [
                { required: true, message: '请输入关注项目的正常范围', trigger: 'blur' }
              ]
        	}
	      }
	    },
	    methods:{
	    	 randomIndex: function () {
		      return Math.floor(Math.random() * this.healthDatas.length);
		    },
		    //查看健康列表
		    getdata(){
		    	Indicator.open('加载中...');
		    	var param = {status:0}
		    	var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?"+location.search.substring(1).split("&")[0]);
		    	resource.get(param).then((response)=>{
		    		this.healthDatas = response.data;
		    		Indicator.close();
		    		response.data.map(item=>{
		    			if(typeof item.is_check ==Boolean){

		    			}else{
		    				item.is_check = item.is_check=="cancel"?false:true;

		    			}
		    		});
		    	},(error)=>{

		    	})
		    },
		    //选择或取消按钮
		    getCheck(item){
		    	var resource = this.$resource(this.$store.state.serverIP+'/health_items/check_item');
		    	resource.save({health_item_id:item.id,is_check:item.is_check}).then((response)=>{

		    	},(error)=>{})
		    },
		    //弹框
	    	submit(formName){
				// this.dataForm.user_id = 2;
				this.$refs[formName].validate((valid) => {
			          if (valid) {
			            // alert('submit!');
			          } else {
			            // console.log('error submit!!');
			            // return false;
			          }
			        });
	    		var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_health_item?"+location.search.substring(1).split("&")[0]);
	    		resource.save(this.dataForm).then((response)=>{
	    			this.dialogVisible = false;
	    			this.getdata();
	    			this.dataForm = {};
	    		},(error)=>{
	    		})
			},
			resetForm(formName) {
			       this.$refs[formName].resetFields();
			     },
	    	//删除健康数据
	    	movers(item){
	    		var resource = this.$resource(this.$store.state.serverIP+"/health_items/delete_health_item?"+location.search.substring(1).split("&")[0]);
	    		this.$confirm('删除本项数据可能会造成相关联数据的丢失, 您确定删除吗?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '删除成功!'
		          });
		    		resource.get({health_item_id:item}).then((response)=>{
						this.getdata();
		    		},(error)=>{
		    		})
		        })
	    	},
	    	eadit(data){
	    		this.Newtitle = "修改关注项目";
	    		this.dialogVisible = true;
	    		this.state = false;
	    		this.dataForm={
	    			name:data.name,
	    			unit:data.unit,
	    			normal_min:data.normal_min,
	    			normal_max:data.normal_max,
	    			id:data.id
	    		}
	    		this.is_admin = data.is_admin;
	    	},
	    	tijiao(){
	    		var resource = this.$resource(this.$store.state.serverIP+"/health_items/edit_health_item?"+location.search.substring(1).split("&")[0]);
	    		resource.save(this.dataForm).then((response)=>{
	    			this.dialogVisible = false;
	    			this.getdata();
	    			this.dataForm = {};
	    		},(error)=>{

	    		})
	    	},
	    	guanzu(){
	    		this.Newtitle = "添加关注项目";
	    		this.is_admin=0;
	    		this.dialogVisible = true;
	    		this.state = true;
	    		this.dataForm = {};
	    	}
	    }
  	};
</script>
<style>
	.an_niu{
		width: 96%;
		margin-left: 2% !important;
	}
	.an_niu p{
		display: inline-block;
	}
	.button_1{
		color:#A21A28;
		border: 1px solid #A21A28;
	}
	.button_2{
		width: 50px;
		color:#3D3D3D;
		border: 1px solid #3D3D3D;
	}
	.el-message-box{
		width: 90%;
	}
	.el-button--small{
		padding: 5px 11px 3px;
	    border-radius: 18px;
	    line-height: 14px;
	}
	/*el-message-box__title{
		text-align: center;
	}*/
	.el-message-box__message{
		display: inline-block;
	}
	.el-message-box__input{
		margin-left: 20px;
    	display: inline-block;
		width: 80%;
	}
	.add_name{
		text-align: center;
	}
	.tan_name{
		background:#A21A28;border:none;
	}
	.title_name{
		padding-left: 10px;
	    padding-right: 10px;
	    margin-top: -36px;
	   	text-align: center;
	   	width: 80%;
	    font-size: 20px;
	    margin-bottom: 26px;
	    color: #c0ccda;
	}
	.el-button--primary:focus, .el-button--primary:hover{
		background:#A21A28;
		color: #fff;
		border:1px solid #A21A28;
	}
	.el-button--primary.is-active, .el-button--primary:active{
		background:#fff;
		color: #1F2D3D;
		border:1px solid #A21A28;
	}
	.el-form-item__error{
		left: 25%;
	}
</style>



