<template>
	<div class="create">
		<!-- <mt-header title="基本信息" class="createName">
			<router-link to="/seeInformation" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->

		<div class="editinfo">
			<mt-field label="姓名" placeholder="请输入姓名" v-model="editList.truename" required></mt-field>
			<mt-cell title="性别" class="informate">
				<el-radio class="radio" v-model="editList.sex" :label="0">男</el-radio>
				<el-radio class="radio" v-model="editList.sex" :label="1">女</el-radio>
			</mt-cell>
			<mt-cell title="年龄" class="age-input">
				<el-input type="number" v-model="editList.age" required></el-input>
				<span>岁</span>
			</mt-cell>
			<mt-field label="民族" placeholder="请输入民族" v-model="editList.nation"></mt-field>
			<mt-cell title="证件类型" class="card-type">
				<el-select v-model="editList.id_type" placeholder="请选择证件">
    				<el-option v-for="item in options" :label="item.label" :value="item.id_type">
    				</el-option>
  				</el-select>
			</mt-cell>
			<mt-cell title="证件号码">
				<el-input type="text" v-model="editList.id_code" :maxlength="18" style="width:100%;"></el-input required>
			</mt-cell>
			<mt-cell title="血型" class="el-btn el-but">
				<mytags :items="blood_types" :type="'radio'" v-model="editList.blood_type">
			  </mytags>
			</mt-cell>
			<mt-cell title="子女数" class="el-btn el-buts">
				<mytags :items="childrens" :type="'radio'" v-model="editList.children">
			  </mytags>
			</mt-cell>
			<mt-cell title="兴趣爱好" class="el-btn el-buts">
				<mytags :items="hobbys" :type="'check'" v-model="editList.hobby_list"></mytags>
			</mt-cell>
			<mt-cell title="特长" class="el-btn el-buts">
				<mytags :items="specialtys" :type="'check'" v-model="editList.speciality_list"></mytags>
			</mt-cell>
			<mt-cell title="学历">
				<el-select v-model="editList.education" placeholder="请选择学历">
    				<el-option v-for="item in option" :label="item.label" :value="item.education">
    				</el-option>
  				</el-select>
			</mt-cell>
			<mt-field label="职务" placeholder="请输入职务" v-model="editList.duty"></mt-field>
			<mt-cell title="职业" class="el-btn el-buts">
				<mytags :items="jobs" :type="'check'" v-model="editList.job_list"></mytags>
			</mt-cell>
			<mt-cell title="职称" class="el-btn el-buts">
				<mytags :items="skill_levels" :type="'check'" v-model="editList.skill_level_list"></mytags>
			</mt-cell>
		</div>
		<div class="next save">
			<mt-button class="next-btn" @click="saveSubmit">保存</mt-button>
		</div>
	</div>
</template>
<script>
import mytags from "../my-tags.vue"
export default {
		components:{mytags},
		data (){
			return {
				childrens:[
					{
						text:"1",
						value:'1'
					},{
						text:"2",
						value:'2'
					},{
						text:"3",
						value:'3'
					},{
						text:"其他",
						value:'4'
					}],
				blood_types: [
					{
						value:'0',
						text:"A"
					},
					{
						value:'1',
						text:"B"
					},
					{
						value:'2',
						text:"AB"
					},
					{
						value:'3',
						text:"O"
					},
					{
						value:'4',
						text:"其他"
					}],
				options: [
					{
						id_type:0,
						label:'身份证'
					},
					{
						id_type:1,
						label:'护照'
					},
					{
						id_type:2,
						label:'军官证'
					},
					{
						id_type:3,
						label:'其他'
					}
				],
				option:[
		    		{
		    			education: 0,
		    			label:'小学'
		    		},
		    		{
		    			education: 1,
		    			label:'初中'
		    		},
		    		{
		    			education: 2,
		    			label:'高中'
		    		},
		    		{
		    			education: 3,
		    			label:'学士'
		    		},
		    		{
		    			education: 4,
		    			label:'硕士'
		    		},
		    		{
		    			education: 5,
		    			label:'博士'
		    		},
		    	],
		    	editList: {},
				hobbys:[],
				jobs: [],
				specialtys:[],
				skill_levels:[]
			}
		},
		created (){
			// var resource = this.$resource(this.$store.state.serverIP+'/users/edit_user_info/?'+location.search.substring(1).split("&")[0]);
			// resource.get().then((response) => {

			// 	this.editList = response.data;
			// 	this.editList.blood_type+="";
			// 	this.editList.children+="";

			// });
			this.editList = JSON.parse(window.localStorage.memberList);
			this.editList.blood_type+="";
			this.editList.children+="";
		},
		methods: {

			getHobby (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/hobbys");
			  	resource.get().then((callback) => {
			  		callback.data.map(nav =>
						this.hobbys.push(
							{
								text:nav,
								value:nav
							}
						))
			  	});
			},
			getJob (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/jobs");
			  	resource.get().then((callback) => {
			  		callback.data.map(nav =>
						this.jobs.push(
							{
								text:nav,
								value:nav
							}
						))
			  	});
			},
			getSpecialty (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/specialitys");
				resource.get().then((callback) => {
					callback.data.map(nav =>
						this.specialtys.push(
							{
								text:nav,
								value:nav
							}
						))
				});
			},
			getSkill_level (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/skill_levels");
				resource.get().then((callback) => {
					callback.data.map(nav =>
						this.skill_levels.push(
							{
								text:nav,
								value:nav
							}
						))
				});
			},
			saveSubmit (){
				//判断必填
				if (!this.editList.truename) {
					this.$toast('请输入姓名');
					return
				}
				if (!this.editList.age) {
					this.$toast('请输入年龄');
					return
				}
				if (!this.editList.id_code) {
					this.$toast('请输入证件号');
					return
				}else if (this.editList.id_code && this.editList.id_code != '') {
	                if ((/^[0-9]{17}[0-9Xx]{1}$/).test(this.editList.id_code) == false) {
	                    this.$toast('请输入正确的身份证号');
	                    return;
	                }
	            }
				if (!this.editList.blood_type) {
					this.$toast('请选择血型');
					return
				}
				if (!this.editList.children) {
					this.$toast('请选择子女数');
					return
				}
				if (!this.editList.job_list) {
					this.$toast('请输入职业');
					return
				}

				//编辑个人信息提交接口
				var resource = this.$resource(this.$store.state.serverIP +"/users/edit_details/?"+location.search.substring(1).split("&")[0]);
			  	resource.save(this.editList).then((response) => {
			    	this.$toast('保存成功');

			    	//获取用户信息
					var resource = this.$resource(this.$store.state.serverIP+'/users/get_user_info/?'+location.search.substring(1).split("&")[0]);
					resource.get().then((response) => {
						window.localStorage.memberList = JSON.stringify(response.data);
					});

			    	setTimeout(() => {
			    		this.$router.go(-1);
			    	},1000);

			  	}, (response) => {
			    	this.$toast('保存失败');
			  	});
			}
		},
		mounted (){
			this.getHobby();
			this.getJob();
			this.getSpecialty();
			this.getSkill_level();
		}
	}
</script>
<style>
.editinfo{
	padding:5px 10px;
}
.save{
	margin-top: 5px;
	margin-bottom: 10px;
}
.card-type .el-select-dropdown{
	top:235px;
}

</style>



