<template>
	<div class="create">
		<!-- <mt-header title="创建账户" class="createName"></mt-header> -->
		<div class="msg">
			<mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-model="form.username" :attr="{ maxlength: 11 }" required></mt-field>
			<mt-field label="验证码" placeholder="请输入验证码" type="number" class="msg-btn" v-model="code" required>
  				<el-button type="primary" :loading="isCodeLoading" :disabled="wait>0" class="click-btn" @click="getCode()">{{CodeMsg}}</el-button>
			</mt-field>
			<mt-field label="密码" placeholder="请输入密码" type="password" v-model="form.password" required></mt-field>
			<mt-field label="确认密码" placeholder="请输入确认密码" type="password" v-model="confirm_password" required></mt-field>
		</div>
		<div class="next">
			<router-link to="/informate">
			    <mt-button class="next-btn" @click.native="clickNext" :disabled="!isNext">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				form: {
					username: '',
					password: ''
				},
				code:this.$route.query.code || "",
				isCodeLoading:false,
				code: '',
				wait:0,
				CodeMsg:'点击获取',
				confirm_code:'',
				confirm_password: ''
			}
		},
		computed:{
			checkPassword:function() {
				return (this.form.password!=''&&this.form.password == this.confirm_password)
			},
			checkCode:function() {
				return (this.code!=''&&this.code == this.confirm_code)
			},
			isNext:function(){
				return (this.form.username!=''&&(this.code!=''&&this.code == this.confirm_code)&&(this.form.password!=''&&this.form.password == this.confirm_password))
			}
		},
		methods: {
			getCode (){
					if(this.form.username&&this.form.username!=''){
						if ((/^1\d{10}$/).test(this.form.username) == false) {
							this.$toast({
								message:'请输入正确的手机号',
								position: 'bottom'
							});
						}else{
							this.CodeMsg='获取验证码';
							this.isCodeLoading = true;
							//会员注册时发送手机验证码短信接口
							var resource = this.$resource(this.$store.state.serverIP + '/commons/sms_api')
							resource.save({username:Number(this.form.username)}).then((response) => {
								this.confirm_code = response.data.data.code;
								this.isCodeLoading = false;
								this.wait = 60;
								this.CodeMsg= this.wait+'s后获取';
								var codeTime = setInterval(()=>{
									this.wait--;
									this.CodeMsg= this.wait+'s后获取';
									if(this.wait<=0){
										clearInterval(codeTime);
										this.CodeMsg = "重新获取";
									}
								},1000)
							},error => {
								this.CodeMsg='点击获取';
								this.isCodeLoading = false;
								this.$toast({
									message:error.data.error,
									position: 'bottom'
								});
							});
						}
					}
					else{
						this.$toast({
							message:'请输入手机号',
							position: 'bottom'
						});
					}
			},
			clickNext (){
				window.localStorage.form = JSON.stringify(this.form);
			}
		}
	}
</script>
<style>
.create{
	font-family: '微软雅黑';
}
.createName{
	background-color: #fff;
	color: #000;
}
.createName h1,.createName .mintui{
	font-size: 20px;
}
.msg{
	padding: 10px 20px;
}
.next{
	padding: 10px 20px;
}
.msg .mint-field-other{
	top: 8px;
	right: 10px;
	border-radius: 3px;
}
.msg .mint-cell{
	border-top: 0px;
	border-bottom: 1px solid #ccc;
}
.msg .mint-field-other .click-btn{
	padding: 0px 5px;
	height: 30px;
	font-size: 13px;
	border-radius: 15px;
	border: 1px solid #e4393c;
	background-color: #fff;
	color: #e4393c;
	outline: none;
}
.next .next-btn{
	background-color: #A21A28;
	outline: none;
	color: #fff;
	width: 100%;
	font-family: '微软雅黑';
}

.mint-cell-title{
	width: 80px !important;
}

</style>



