<template>
	<div class="create">
		<div class="msg">
			<div class="hobby">
				<h3>职业</h3>
				<mytags :items="jobs" :type="'check'" v-model="form.job_list"></mytags>
			</div>
			<mt-cell title="学历">
				<el-select v-model="form.education" placeholder="请选择学历">
    				<el-option v-for="item in option" :label="item.label" :value="item.education">
    				</el-option>
  				</el-select>
			</mt-cell>
			<mt-field label="职务" placeholder="请输入职务" v-model="form.duty"></mt-field>
			<div class="hobby">
				<h3>职称</h3>
				<mytags :items="skill_levels" :type="'check'" v-model="form.skill_level_list"></mytags>
			</div>
			<div class="confirm">
				<input type="checkbox" name="confirm" v-model="form.confirm" style="width:16px;height:16px;vertical-align:middle;">
				<label for="confirm">已阅读并同意
					<a @click="rulesClick">《条款及细则》</a>
				</label>
			</div>
		</div>
		<div class="next">
			<mt-button class="next-btn" @click="submit" :disabled="!isNext">创建账户</mt-button>
		</div>
	</div>
</template>
<script>
	import mytags from "../my-tags.vue"
	export default {
		components:{mytags},
		data (){
			this.getJob();
			this.getSkill_level();
			const form = Object.assign(
				{
					job_list:"",
					skill_level_list:"",
					confirm:'',
					education:null,
					duty:''
				},
				JSON.parse(window.localStorage.form)
			)
			return {
				form,
				jobs:[],
		    	skill_levels:[],
		    	option:[
		    		{
		    			education: 0,
		    			label:'小学'
		    		},
		    		{
		    			education: 1,
		    			label:'初中'
		    		},
		    		{
		    			education: 2,
		    			label:'高中'
		    		},
		    		{
		    			education: 3,
		    			label:'学士'
		    		},
		    		{
		    			education: 4,
		    			label:'硕士'
		    		},
		    		{
		    			education: 5,
		    			label:'博士'
		    		},
		    	]
			}
		},
		computed: {
			isNext: function () {
				return (this.form.job_list!=''&&this.form.education!=null&&this.form.duty!=''&&this.form.skill_level_list!=''&&this.form.confirm!='')
			}
		},
		methods: {
			getJob (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/jobs");
			  	resource.get().then((callback) => {
			  	callback.data.map(nav =>
					this.jobs.push(
						{
							text:nav,
							value:nav
						}
					))
			  	});
			},
			getSkill_level (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/skill_levels");
				resource.get().then((callback) => {
					callback.data.map(nav =>
						this.skill_levels.push(
							{
								text:nav,
								value:nav
							}
						))
				});
			},
			submit(){
				//判断必填
				if (!this.form.duty) {
					this.$toast('职务');
					return
				}
				if (!this.form.skill_level_list) {
					this.$toast('请选择职称');
					return
				}
				if (!this.form.confirm) {
					this.$toast('未同意条款细则');
					return
				}

				//创建账户提交接口
				this.form.sex = Number(this.form.sex);
				var resource = this.$resource(this.$store.state.serverIP +"/users/user_vip/?"+location.search.substring(1).split("&")[0]);
			  	resource.save(this.form).then((response) => {
			  		this.$toast({
						message:'注册成功',
						position: 'bottom'
					});

			  		window.localStorage.removeItem('form');

					setTimeout(() =>{
						this.$router.push('/member');
					},1000);

			 	}, (response) => {
			    	this.$toast({
						message:response.data.error,
						position: 'bottom'
					});
			  });
			},
			rulesClick(){
				window.localStorage.form = JSON.stringify(this.form);
				this.$router.push('/rules');
			}
    	}
	}
</script>
<style>
.hobby{
	margin-top: 0px;
	overflow: hidden;
}

.hobby h3{
	text-align: center;
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #ccc;
	font-size: 22px;
	font-weight: normal;
}
.confirm{
	margin: 20px 0;
}
.confirm label{
	font-size: 16px;
}
.confirm label a{
	border-bottom:1px solid #A21A28;
}

</style>



