<template>
  <div style="padding:20px 16px;" >
    <el-row class="card"v-for="item in items" v-cloak>
      <el-col :span="6" class="label" style="text-align:right">{{item.name}}</el-col>
      <el-col :span="12" class="value" style="text-align:left">
        <div class="input"   @click="openPopup(item.id)" v-html="item.value"></div>
        <myPicker :item.sync="item"></myPicker>

      </el-col>
      <el-col :span="6" style="text-align:left;height:70px;line-height:70px">
        <span>{{item.sub_unit ||item.unit}}</span>
      </el-col>
    </el-row>


  <!-- <el-input type="textarea" disabled="disabled"></el-input> -->
  <mt-button class="submit" @click.native="submit">保存</mt-button>
  </div>


</template>
<script>
import { Indicator } from 'mint-ui';
import myPicker from "./picker.vue";
export default {
  components:{myPicker},
  data() {
    return {
      items:[],
      form:{}
    };
  },
  methods: {
    openPopup (id){
      this.items.map(item=>{
        if(item.id==id){
          setTimeout(()=>{
            item.isPicker =true;
          },300)
        }else{
          item.isPicker =false;
        }
      })
    },

    checkForm (){
      if(this.items.length>0){
        var list=[];
        for(var ins of this.items){
          list.push(ins.value)
          if(ins.value==""){
            this.$toast("请输入"+ins.name);
            return true;
            break;
          }
        }
        this.form.content=list.join(",")||"";
        this.form.health_item_id=this.$route.params.id;
        return false;
      }else{
        return true
      }
    },
    submit (){
      if(this.checkForm())return false;
      Indicator.open("正在保存...");
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_item_record");
      resource.save(this.form).then(callback => {
        Indicator.close();
        this.$toast("保存成功");
        setTimeout(()=>{
          this.$router.go(-1);
        },1000)

      });
    },
    getArray (min,max){
      var arr=[];
      for (var i =min;i<=max;i++){
        arr.push(i)
      }
      return arr
    }
  },
  mounted () {
    Indicator.open('正在加载...');
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/health_item?health_item_id="+this.$route.params.id+"&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        var items = callback.data;
        if(items.subitems.length<=0){
          items.value="";
          items.isPicker=false;
            try{
              var  list = items.value_range.split(",");

              if(items.normal_min!=null&&(Number(list[0])>Number(items.normal_min))){
                this.$toast("数据异常,正常值不能小于最小值");
                this.$router.go(-1);
                return false;
              }
               if(items.normal_max!=null&&(Number(list[1])<Number(items.normal_max))){
                 this.$toast("数据异常,正常值不能大于最大值");
                 this.$router.go(-1);
                 return false;
              }
              if(list[0].indexOf(".")>0&&list[1].indexOf(".")>0){
                list[0] = list[0].split(".");
                list[1] = list[1].split(".");
                if(list[0][1]==""||list[1][1]==""){
                  this.$toast("数据异常,格式不一致");
                  this.$router.go(-1);
                }
                var floas = list[0][1].length>list[1][1].length?list[0][1].length:list[1][1].length;
                var floa =1;
                for(var i=0;i<floas;i++){
                  floa+="0";
                }
                items.slots=[
                  {
                    flex: 1,
                    defaultIndex:0,
                    values:this.getArray(Number(list[0][0]),Number(list[1][0])),
                    className: '',
                    textAlign: 'center'
                  },{
                    divider: true,
                    content: '.',
                    className: 'slot2'
                  }, {
                    flex: 1,
                    defaultIndex:0,
                    values: this.getArray(Number(0),Number(floa)),
                    className: 'slot3',
                    textAlign: 'center'
                  }
                ]
                var normal_min =  items.normal_min+"";
                normal_min =  normal_min.split(".");
                console.log(normal_min);
                items.slots[0].defaultIndex = items.slots[0].values.indexOf(Number(normal_min[0]))>0?items.slots[0].values.indexOf(Number(normal_min[0])):0;
                items.slots[2].defaultIndex = items.slots[2].values.indexOf(Number(normal_min[1]));
              }else if(list[0].indexOf(".")<0&&list[1].indexOf(".")<0){
                items.slots=[{
                  flex: 1,
                  defaultIndex:0,
                  values:this.getArray(Number(list[0]),Number(list[1])),
                  className: '',
                  textAlign: 'center'
                }]
                items.slots[0].defaultIndex = items.slots[0].values.indexOf(items.normal_min)>0?items.slots[0].values.indexOf(items.normal_min):0;
              }else {
                this.$toast("数据异常,格式不一致");
                this.$router.go(-1);
              }
              this.items.push(items);
            }catch(error){
              console.log(error)
              this.$toast("数据异常");
              this.$router.go(-1);
            }
        }else{
            items.subitems.map(nav=>{
            var list =[];
            nav.value="";
            nav.isPicker=false;
            try{
              list = nav.sub_value_range.split(",");
              if(nav.sub_min!=null&&(Number(list[0])>Number(nav.sub_min))){
                this.$toast("数据异常,正常值不能小于最小值");
                this.$router.go(-1);
                return false;
              }
               if(nav.sub_max!=null&&(Number(list[1])<Number(nav.sub_max))){
                 this.$toast("数据异常,正常值不能大于最大值");
                 this.$router.go(-1);
                 return false;
              }
              if(list[0].indexOf(".")>0&&list[1].indexOf(".")>0){
                list[0] = list[0].split(".");
                list[1] = list[1].split(".");
                if(list[0][1]==""||list[1][1]==""){
                  this.$toast("数据异常,格式不一致");
                  this.$router.go(-1);
                }
                var floas = list[0][1].length>list[1][1].length?list[0][1].length:list[1][1].length;
                var floa =1;
                for(var i=0;i<floas;i++){
                  floa+="0";
                }
                nav.slots=[
                  {
                    defaultIndex:0,
                    flex: 1,
                    values:this.getArray(Number(list[0][0]),Number(list[1][0])),
                    className: '',
                    textAlign: 'center'
                  },{
                    divider: true,
                    content: '.',
                    className: 'slot2'
                  }, {
                    flex: 1,
                    defaultIndex:0,
                    values: this.getArray(Number(0),Number(floa)),
                    className: 'slot3',
                    textAlign: 'center'
                  }

                ]
                var normal_min =  items.sub_min+"";
                normal_min =  normal_min.split(".");
                console.log(normal_min);
                nav.slots[0].defaultIndex = nav.slots[0].values.indexOf(Number(normal_min[0]))>0?nav.slots[0].values.indexOf(Number(normal_min[0])):0;
                nav.slots[2].defaultIndex = nav.slots[2].values.indexOf(Number(normal_min[1]));
              }else if(list[0].indexOf(".")<0&&list[1].indexOf(".")<0){
                nav.slots=[{
                  flex: 1,
                  defaultIndex:0,
                  values:this.getArray(Number(list[0]),Number(list[1])),
                  className: '',
                  textAlign: 'center'
                }]
                nav.slots[0].defaultIndex = nav.slots[0].values.indexOf(nav.sub_min)>0?nav.slots[0].values.indexOf(nav.sub_min):0;

              }else {
                this.$toast("数据异常,格式不一致");
                this.$router.go(-1);
              }
              this.items.push(nav);
            }catch(error){
              console.log(error)
              this.$toast("数据异常");
              this.$router.go(-1);
            }
          })

        }

        Indicator.close();
      },error=>{
        Indicator.close();
      });




  }

}
</script>

<style   lang="scss">
.card{
  text-align: center;
  overflow: hidden;
  margin: 0px auto;
  margin-bottom: 30px;
  .label,.value {
    height: 70px;
    color: #323232;
    line-height: 70px;
  }
  .value .input{
      text-align: center;
      border: 1px solid #ccc;
      width: 120px;
      font-weight: 100;
      color: #b01421;
      font-size: 50px;
      height: 66px;
      line-height: 66px;
      margin-left: 10px;
      border-radius: 4px;
      outline: none;
  }
}

.submit{
	background-color: #A21A28;
	outline: none;
	color: #fff;
	width: 100%;
  margin: 20px auto;
	font-family: '微软雅黑';
}
</style>



